<template>
	<Map :config="config"></Map>
</template>

<script>
	import Map from '@/components/screen/layer/base/Map'
	import ChinaCity from '../../../map/coordinate/china-city'
	import _ from 'lodash'

	export default {
		name: 'FlowMap',
		props: ['config'],
		data() {
			return {}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: 'transparent',
					visualMap: {
						show: true,
						min: 0,
						max: 100,
						calculable: true,
						color: ['#ff3333', '#ffff00', '#00ffff'],
						left: 'left',
						itemWidth: 20,
						itemHeight: 100,
						textStyle: {
							color: '#fff'
						}
					},
					geo: {
						map: 'china',
						label: {
							emphasis: {
								show: false
							}
						},
						roam: true,
						layoutCenter: ['50%', '53%'],
						layoutSize: '108%',
						itemStyle: {
							normal: {
								color: 'rgba(51, 69, 89, .5)',
								borderColor: 'rgba(100,149,237,1)'
							},
							emphasis: {
								color: 'rgba(37, 43, 61, .5)'
							}
						}
					},
					series: [{
						type: 'lines',
						zlevel: 2,
						effect: {
							show: true,
							period: 4,
							trailLength: 0.02,
							symbol: 'circle',
							symbolSize: 5,
						},
						lineStyle: {
							normal: {
								width: 1,
								opacity: 0.6,
								curveness: 0.2
							}
						},
						data: null
					}, {
						type: 'effectScatter',
						coordinateSystem: 'geo',
						zlevel: 2,
						rippleEffect: {
							period: 4,
							brushType: 'stroke',
							scale: 2.5
						},
						label: {
							normal: {
								show: true,
								position: 'right',
								offset: [5, 0],
								formatter: '{b}'
							},
							emphasis: {
								show: true
							}
						},
						symbol: 'circle',
						symbolSize: this.calcSymbolSize,
						itemStyle: {
							normal: {
								show: false,
								color: '#f00'
							}
						},
						data: null
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{from: '上海市', value: 100, to: '北京市'},
					{from: '广州市', value: 70, to: '北京市'},
					{from: '哈尔滨市', value: 30, to: '北京市'},
					{from: '青岛市', value: 50, to: '北京市'},
					{from: '南昌市', value: 20, to: '北京市'},
					{from: '银川市', value: 10, to: '北京市'},
					{from: '拉萨市', value: 80, to: '北京市'},
					{from: '西安市', value: 55, to: '北京市'},
					{from: '乌鲁木齐市', value: 90, to: '北京市'}
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [{
					name: 'click',
					comment: '点击散点事件',
					filter: function (params) {
						if (params.componentType === "series" && params.seriesType === "effectScatter") {
							return true
						} else {
							return false;
						}
					},
					binders: [
						{name: 'name', comment: '数据名', prop: 'name', state: null},
						{name: 'longitude', comment: '经度', prop: 'value[0]', state: null},
						{name: 'latitude', comment: '纬度', prop: 'value[1]', state: null},
						{name: 'value', comment: '数据值', prop: 'value[2]', state: null},
					]
				}, {
					name: 'click',
					comment: '点击底图事件',
					filter: function (params) {
						if (params.componentType === "geo") {
							return true
						} else {
							return false;
						}
					},
					binders: [
						{name: 'name', comment: '行政区划名', prop: 'name', state: null},
					]
				}]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						from: null,
						to: null,
						value: null
					},
					// 储存一些临时系数
					variable: {
						symbolSize: 1
					}
				}
			},
			/**
			 * 计算散点大小
			 */
			calcSymbolSize(val) {
				return 4 + val[2] * this.config.transform.variable.symbolSize / 10
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let lineData = []
				let scatterData = []
				let propFrom = this.config.transform.mapper.from || 'from'
				let propTo = this.config.transform.mapper.to || 'to'
				let propValue = this.config.transform.mapper.value || 'value'
				for (let i = 0; i < this.config.data.length; i++) {
					let item = this.config.data[i]
					let startCity = item[propFrom]
					let endCity = item[propTo]
					let value = item[propValue]
					let startCoord = ChinaCity.get(startCity)
					let endCoord = ChinaCity.get(endCity)
					if (startCoord && endCoord) {
						lineData.push([{
							coord: startCoord,
							value: value
						}, {
							coord: endCoord,
						}])
						scatterData.push({
							name: startCity,
							value: startCoord.concat(value)
						})
					}
				}
				this.config.option.series[0].data = lineData
				this.config.option.series[1].data = scatterData
			}
		},
		components: {
			'Map': Map
		},
	}
</script>
