import Base from './Base'
import axios from 'axios'
import API from '../../../config/api'
import CONST from '../../../config/const'

class DatabaseData extends Base {
	constructor(component) {
		super(component)
		let proxy = this.component.config.proxy
		if (!proxy || !proxy.dsType || !proxy.dsId || !proxy.sql) {
			return
		}
		let sql = proxy.sql
		let keys = sql.match(/:\w+/g)
		if (!keys || !keys.length) {
			return
		}
		let store = {}
		let vuex = this.component.$root.$store
		for (let i = 0; i < keys.length; i++) {
			let key = keys[i].substring(1)
			let storeProp = 'screen.design.interaction.' + key
			store[key] = vuex.commit(CONST.MUTATIONS.GET, {prop: storeProp}) || null
			vuex.commit(CONST.MUTATIONS.REGISTER, {
				// 绑定哪个vuex状态
				store: storeProp,
				// 当前的绑定对象
				comp: this.component,
				// 当vuex状态变更时，应该修改绑定对象的哪个属性
				prop: "config.store." + key
			})
		}
		this.component.config.store = store
	}

	/**
	 * 根据proxy配置属性生成代理，并将参数进行格式化，其中参数名为空的去除
	 * 如果参数名值以冒号(:)开头，则说明引用了全局状态，此时根据状态名获取值，并写入参数对象中
	 */
	do() {
		let proxy = this.component.config.proxy
		if (!proxy || !proxy.dsType || !proxy.dsId || !proxy.sql) {
			return new Promise((resolve, reject) => {
			})
		}
		let store = this.component.config.store
		let sql = proxy.sql
		for (let key in store) {
			let regex = new RegExp(':' + key, 'g')
			let val = store[key]
			sql = sql.replace(regex, val)
		}
		// 返回Promise方便外部调用时进行回调
		return new Promise((resolve, reject) => {
			axios({
				url: API.DATA_SOURCE.QUERY,
				method: CONST.AJAX.POST,
				data: {
					dsId: proxy.dsId,
					type: proxy.dsType,
					sql: sql
				},
			}).then((response) => {
				resolve(response)
			}).catch((error) => {
				reject(error)
			})
		})
	}
}

export default DatabaseData
