<template>
	<div :style="calcStyle"
	     class="chart-ins">
		<div :id="config.id"
		     :style="{width:config.style.width + 'px', height: config.style.height + 'px'}">
		</div>
	</div>
</template>

<script>
	import echarts from "echarts"
	import '../../../map/basemap/echarts-china'
	import DataProxy from "../../../proxy/DataProxy"
	import CONST from "../../../../config/const"
	import _ from "lodash"

	export default {
		name: "Map",
		props: ['config'],
		data() {
			return {
				// chart图表实例
				chart: null,
				// 数据代理实例
				dataProxy: null,
				// 数据代理线程
				proxyThread: null
			}
		},
		created() {
			this.initConfig()
			this.initStyle()
			this.initProxy()
			this.initTransform()
		},
		mounted() {
			this.init()
		},
		/**
		 * 销毁时注销组件
		 */
		beforeDestroy() {
			this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
				comp: this
			})
		},
		computed: {
			/**
			 * 计算容器样式
			 */
			calcStyle() {
				return {
					width: this.config.style.width + 'px',
					height: this.config.style.height + 'px',
					opacity: this.config.style.opacity,
					display: this.config.style.display,
				}
			}
		},
		methods: {
			/**
			 * 调用父组件的初始化方法，初始化配置
			 */
			initConfig() {
				if (this.$parent.initOption) {
					this.$parent.initOption()
				}
				if (this.$parent.initData) {
					this.$parent.initData()
				}
				if (this.$parent.initEvent) {
					this.$parent.initEvent()
				}
				if (this.$parent.initProxy) {
					this.$parent.initProxy()
				}
				if (this.$parent.initTransform) {
					this.$parent.initTransform()
				}
			},
			/**
			 * 初始化组件
			 */
			init() {
				this.initIns()
				this.initEvent()
				this.instanceProxy()
			},
			/**
			 * 初始化chart实例
			 */
			initIns() {
				let dom = document.getElementById(this.config.id)
				this.chart = echarts.init(dom)
			},
			/**
			 * 初始化样式，对新组件使用默认样式
			 */
			initStyle() {
				this.config.style = this.config.style || {
					top: 30,
					left: 30,
					width: 400,
					height: 300,
					opacity: 1,
					display: 'block',
				}
			},
			/**
			 * 初始化数据代理的配置参数
			 */
			initProxy() {
				let proxy = _.assignIn({}, {
					// 类型，0-API / 1-静态数据 / 2-数据库,
					type: 1,
					// 自动更新
					autoRequest: false,
					// 自动更新间隔
					interval: 60,
					// url
					url: null,
					// 参数
					param: [{key: null, value: null}],
				}, this.config.proxy)
				this.config.proxy = proxy
			},
			/**
			 * 实例化proxy，并缓存为组件属性
			 */
			instanceProxy() {
				this.dataProxy = new DataProxy(this)
			},
			/**
			 * 初始化transform
			 */
			initTransform() {
				let transform = _.assignIn({}, {
					// 从源数据的什么属性获取展示用的数据集
					prop: null,
				}, this.config.transform)
				this.config.transform = transform
			},
			/**
			 * 注册所有传入的事件
			 */
			initEvent() {
				if (!this.config.event) {
					return
				}
				let me = this
				// 注册所有事件
				for (let i = 0; i < me.config.event.length; i++) {
					let event = me.config.event[i]
					me.chart.on(event.name, function (params) {
						let filter = event.filter
						if (filter && !filter(params)) {
							return
						}
						/*
             * 当事件触发时，遍历事件的所有参数，当参数与全局状态存在绑定关系时，
             * 向全局状态提交状态变更的请求，全局状态接收到最新的值后，会向订阅该状态的组件进行分发
             */
						if (!event.binders || !event.binders.length) {
							return
						}
						for (let j = 0; j < event.binders.length; j++) {
							let binder = event.binders[j]
							if (!binder.state) {
								continue
							}
							let value
							if (binder.prop) {
								value = _.get(params, binder.prop)
							} else {
								let dataset = this.config.data
								value = dataset[params.dataIndex][binder.name]
							}
							me.commit("screen.design.interaction." + binder.state, value)
						}
					})
				}
			},
			/**
			 * 使用数据代理获取数据，并将数据载入配置项中，然后重新加载配置项，渲染组件
			 */
			render() {
				clearInterval(this.proxyThread)
				this.proxyData()
				if (this.config.proxy.autoRequest && this.config.proxy.interval) {
					this.proxyThread = setInterval(this.proxyData, this.config.proxy.interval * 1000)
				}
			},
			/**
			 * 代理数据
			 */
			proxyData() {
				this.dataProxy.do()
						.then((data) => {
							this.config.data = data
						})
						.catch(() => {
						})
						.finally(() => {
							this.loadData()
							this.loadOption()
						})
			},
			/**
			 * 调用父组件的handler方法，handler定义了将数据载入option的过程，
			 * 因为option配置项是根据具体的报表类型而不同的，所以handler必须定义在父组件中
			 */
			loadData() {
				if (!this.$parent || !this.$parent.handler) {
					return
				}
				this.$parent.handler()
			},
			/**
			 * 加载最新的配置项，对chart实例进行修改
			 */
			loadOption() {
				try {
					this.chart.setOption(this.config.option, true)
				} catch (e) {
				}
			},
			/**
			 * 向全局状态管理发起状态修改请求
			 */
			commit(stateProp, value) {
				this.$store.commit(CONST.MUTATIONS.MODIFY, {
					store: stateProp,
					value: value
				})
			},
			/**
			 * 重新加载报表
			 */
			reload() {
				this.render()
			},
			/**
			 * chart刷新尺寸
			 */
			resize() {
				this.chart.resize({
					height: this.config.style.height,
					width: this.config.style.width,
					silent: true
				})
			}
		},
		watch: {
			"config.style.height": function () {
				this.resize()
			},
			"config.style.width": function () {
				this.resize()
			},
			/**
			 * 监听option，发生变更时重新载入最新option
			 */
			"config.option": {
				deep: true,
				handler: function () {
					this.loadOption()
				}
			},
			/**
			 * 监听proxy，发生变更时，重新实例化数据代理，并渲染组件
			 */
			"config.proxy": {
				deep: true,
				handler: function () {
					this.instanceProxy()
					this.render()
				}
			},
			/**
			 * 监听全局状态绑定，发生变更时重新渲染组件
			 */
			"config.store": {
				deep: true,
				handler: function () {
					this.render()
				}
			},
			/**
			 * 监听数据转换规则，发生变更时，根据最新规则载入数据，并且修改chart的option配置项
			 */
			"config.transform": {
				deep: true,
				handler: function () {
					this.loadData()
					this.loadOption()
				}
			},
			/**
			 * 监听从数据转为option的方法，发生变更时载入数据，并且修改chart的option配置项
			 */
			"config.handler": {
				deep: true,
				handler: function () {
					this.loadData()
					this.loadOption()
				}
			},
		}
	}
</script>
