<template>
	<div class="menu">
		<template v-for="(node, i) in $store.state.screen.design.menu">
			<div class="node"
			     :key="i"
			     @mouseover="node.show = true"
			     @mouseleave="node.show = false">
				<el-button type="primary"
				           size="mini"
				           circle>
					<i :class="'icon-' + node.icon" class="icon iconfont"></i>
				</el-button>
				<el-tabs v-show="node.show"
				         type="border-card"
				         tab-position="left">
					<template v-for="(category, j) in node.categories">
						<el-tab-pane :key="j">
							<span slot="label"><i :class="'icon-' + category.icon" class="icon iconfont"></i></span>
							<template v-for="(item, k) in category.items">
								<div class="item-card" :key="k" @click="addComponent(i, j, k)">
									<img :src="'./img/screen/components/' + item.category + '/' + item.name + '.png'">
									<div class="item-name">{{item.text}}</div>
								</div>
							</template>
						</el-tab-pane>
					</template>
				</el-tabs>
			</div>
		</template>
	</div>
</template>

<script>
	export default {
		name: "Menu",
		props: ["config", "state"],
		data() {
			return {
				showPanel: false,
			}
		},
		methods: {
			/**
			 * 添加一个组件
			 * @param i
			 * @param j
			 * @param k
			 */
			addComponent(i, j, k) {
				let item = this.$store.state.screen.design.menu[i].categories[j].items[k]
				if (!this.config.components) {
					this.config.components = {}
				}
				if (!this.config.sequence) {
					this.config.sequence = []
				}
				let id = "cmp-" + new Date().getTime()
				this.config.sequence.push(id)
				this.$set(this.config.components, id, {
					text: item.text,
					category: item.category,
					name: item.name,
					config: {
						// id
						id: id,
						// 样式
						style: null,
						// 功能配置
						option: null,
						// 与全局状态的绑定规则，当全局状态发生变更，会分发消息到此处，
						// 组件内可以通过监听该状态，来更新自身的状态
						store: null,
						// 静态数据或者从远端获取的数据存储
						data: null,
						// 数据代理，获取数据的方式
						proxy: null,
						// 数据转换，proxy获取到的数据，经过transform转换规则的处理，存储到data属性中
						transform: null,
						// 将data数据根据展示需要，转换到option中
						handler: null,
						// 事件，以及事件中的参数与全局状态的绑定配置
						event: null
					}
				})
			}
		}
	}
</script>

