<template>
	<Chart :config="config"></Chart>
</template>

<script>
	import Chart from '@/components/screen/layer/base/Chart'
	import _ from 'lodash'

	export default {
		name: 'BaseBar',
		props: ['config'],
		data() {
			return {}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: 'transparent',
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'line',
							lineStyle: {
								opacity: 0
							}
						},
						formatter: function (prams) {
							return prams[0].data
						}
					},
					grid: {
						left: '0%',
						right: '0%',
						bottom: '5%',
						top: '7%',
						height: '85%',
						containLabel: true,
					},
					xAxis: [{
						type: 'category',
						gridIndex: 0,
						data: null,
						axisTick: {
							alignWithLabel: true
						},
						axisLine: {
							lineStyle: {
								color: '#0c3b71'
							}
						},
						axisLabel: {
							show: true,
							color: 'rgb(170,170,170)',
							fontSize: 16
						}
					}],
					yAxis: [{
						type: 'value',
						gridIndex: 0,
						splitLine: {
							show: false
						},
						axisTick: {
							show: false
						},
						axisLine: {
							lineStyle: {
								color: '#0c3b71'
							}
						},
						axisLabel: {
							color: 'rgb(170,170,170)',
							formatter: '{value}'
						}
					}, {
						type: 'value',
						splitLine: {
							show: false
						},
						axisLine: {
							show: false
						},
						axisTick: {
							show: false
						},
						axisLabel: {
							show: false
						}
					}],
					series: [{
						name: '',
						type: 'bar',
						barWidth: '50%',
						xAxisIndex: 0,
						yAxisIndex: 0,
						label: {
							normal: {
								show: true,
								position: 'top',
								textStyle: {
									color: '#ffc72b',
									fontSize: 16
								}
							}
						},
						itemStyle: {
							normal: {
								color: '#0286ff'
							}
						},
						data: null,
						zlevel: 2
					}, {
						name: '背景',
						type: 'bar',
						barWidth: '50%',
						xAxisIndex: 0,
						yAxisIndex: 1,
						barGap: '-100%',
						data: null,
						itemStyle: {
							normal: {
								color: 'rgba(255,255,255,0.1)'
							}
						},
						zlevel: 1
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{x: '周一', y: 80},
					{x: '周二', y: 87},
					{x: '周三', y: 51},
					{x: '周四', y: 81},
					{x: '周五', y: 23},
					{x: '周六', y: 45},
					{x: '周日', y: 33}
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [
					{
						name: 'click',
						comment: '点击事件',
						binders: [
							{name: 'name', comment: '数据名', prop: 'name', state: null},
							{name: 'value', comment: '数据值', prop: 'value', state: null},
						]
					}
				]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						x: null,
						y: null
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let data = this.config.data
				let mapper = this.config.transform.mapper
				let propX = mapper.x || 'x'
				let propY = mapper.y || 'y'
				let xAxis = []
				let series0 = []
				let series1 = []
				for (let i = 0; i < data.length; i++) {
					xAxis.push(data[i][propX])
					series0.push(data[i][propY])
					series1.push(100)
				}
				this.config.option.xAxis[0].data = xAxis
				this.config.option.series[0].data = series0
				this.config.option.series[1].data = series1
			}
		},
		watch: {
			/**
			 * 因为这个柱状图样式特殊，系列0展示数据，系列1作为系列0的背景
			 * 所以此处将系列1的柱子宽度与系列0的宽度绑定
			 */
			'config.option.series': {
				deep: true,
				handler: function (val) {
					this.config.option.series[1].barWidth = this.config.option.series[0].barWidth
				}
			},
		},
		components: {
			'Chart': Chart
		},
	}
</script>
