<template>
	<Chart :config="config"></Chart>
</template>

<script>
	import Chart from '@/components/screen/layer/base/Chart'
	import _ from 'lodash'

	export default {
		name: 'BaseGauge',
		props: ['config'],
		data() {
			return {
				seriesOption: []
			}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: "rgba(0,0,0,0)",
					series: [{
						name: '刻度',
						type: 'gauge',
						radius: 100,
						min: 0,
						max: 16,
						splitNumber: 8,
						startAngle: 225,
						endAngle: -45,
						axisLine: {
							show: true,
							lineStyle: {
								width: 1,
								color: [
									[1, 'rgba(0,0,0,0)']
								]
							}
						},
						axisLabel: {
							show: true,
							color: '#dedede',
							distance: -20,
							fontSize: 16
						},
						axisTick: {
							show: true,
							splitNumber: 7,
							lineStyle: {
								width: 1,
							},
							length: -8
						},
						splitLine: {
							show: true,
							length: -20,
							lineStyle: {
								width: 2,
								color: '#5d5d5d'
							}
						},
						detail: {
							show: false
						},
						pointer: {
							show: false
						}
					}, {
						type: 'gauge',
						radius: 80,
						splitNumber: 0,
						startAngle: 225,
						endAngle: -45,
						axisLine: {
							show: true,
							lineStyle: {
								width: 15,
								color: [
									[0.2, '#41C468'],
									[0.4, '#70C1B3'],
									[0.6, '#00A1E9'],
									[0.8, '#18c8ff'],
									[
										1, '#EE4444'
									]
								]
							}
						},
						splitLine: {
							show: false,
						},
						axisLabel: {
							show: false
						},
						axisTick: {
							show: false
						},
						pointer: {
							show: true
						},
						title: {
							show: true,
							offsetCenter: [0, 60],
							textStyle: {
								color: '#fff',
								fontSize: 14
							}
						},
						detail: {
							show: true,
							offsetCenter: [0, 90],
							color: '#ffffff',
							formatter: function (params) {
								return params
							},
							textStyle: {
								fontSize: 28
							}
						},
						data: [{
							name: "交通拥堵指数",
							value: 90.61
						}]
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: "交通拥堵指数", value: 90.61}
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [
					{
						name: 'click',
						comment: '点击事件',
						binders: [
							{name: 'name', comment: '数据名', prop: 'name', state: null},
							{name: 'value', comment: '数据值', prop: 'value', state: null},
						]
					}
				]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						name: null,
						value: null,
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				if (!this.config.data || !this.config.data.length) {
					return
				}
				let nameProp = this.config.transform.mapper.name || 'name'
				let valueProp = this.config.transform.mapper.value || 'value'
				let series = []
				for (let i = 0; i < this.config.data.length; i++) {
					let item = this.config.data[i]
					series.push({
						name: item[nameProp],
						value: item[valueProp]
					})
				}
				this.config.option.series[1].data = series
			}
		},
		components: {
			'Chart': Chart
		},
	}
</script>
