<template>
	<div class="screen-overview">
		<div class="logo"></div>
		<div class="title">VISION</div>
		<el-menu :default-active="menuIndex"
		         class="el-menu-demo"
		         mode="horizontal"
		         background-color="rgba(0,0,0,0)"
		         text-color="#fff"
		         active-text-color="#00BAFF" @select="selectMenu">
			<el-menu-item index="0"><i class="icon iconfont icon-layer">我的可视化</i></el-menu-item>
			<el-menu-item index="1"><i class="icon iconfont icon-datasource">我的数据源</i></el-menu-item>
		</el-menu>
		<div class="system">
			<i class="icon iconfont icon-user"><span>Tyrival</span></i>
			<i class="icon iconfont icon-logout"></i>
		</div>
		<!-- 可视化成果 -->
		<div v-show="menuIndex==='0'"
		     class="container container-result">
			<div class="card add" @click="addScreen">
				<div class="content">
					<i class="icon iconfont icon-plus"></i>
					<div>新建可视化</div>
					<el-tooltip class="item" effect="dark" content="导入配置" placement="bottom">
						<i class="el-icon-upload" @click="showImporter"></i>
					</el-tooltip>
				</div>
			</div>
			<template v-if="screen.list && screen.list.length">
				<template v-for="(item, i) in screen.list">
					<div class="card">
						<img :src="item.cover" class="img-cover">
						<div class="title-bar">
							<el-row>
								<el-col :span="16" class="name">{{item.name || '未命名'}}</el-col>
								<el-col :span="8" class="state">
									<el-tag v-if="item.state === 0" size="mini" type="info">未发布</el-tag>
									<el-tag v-else size="mini" type="success">已发布</el-tag>
								</el-col>
							</el-row>
						</div>
						<div class="toolbar">
							<div class="delete">
								<el-tooltip class="item" effect="dark" content="删除" placement="top">
									<i class="icon iconfont icon-delete" @click="deleteScreen(i)"></i>
								</el-tooltip>
							</div>
							<div class="edit">
								<el-button type="primary" size="mini" round @click="editScreen(i)"> 编 辑</el-button>
							</div>
							<el-tooltip class="item" effect="dark" content="导出" placement="bottom">
								<i class="icon iconfont icon-download" @click="exportScreen(i)"></i>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" content="预览" placement="bottom">
								<i class="icon iconfont icon-view" @click="previewScreen(i)"></i>
							</el-tooltip>
							<el-tooltip class="item" effect="dark" content="发布" placement="bottom">
								<i class="icon iconfont icon-plane" @click="publishScreen(i)"></i>
							</el-tooltip>
						</div>
					</div>
				</template>
			</template>
		</div>
		<!-- 数据源成果 -->
		<div v-show="menuIndex==='1'"
		     class="container container-result">
			<div class="ds-toolbar">
				<div class="ds-add" @click="addDataSource">
					<div class="ds-add-text"><i class="icon iconfont icon-plus"></i> 新建数据源</div>
				</div>
			</div>
			<div class="ds-list">
				<template v-if="dataSource.list && dataSource.list.length">
					<template v-for="(item, i) in dataSource.list">
						<el-col :span="12">
							<div class="ds-block">
								<div class="ds-tile">
									<div class="ds-icon">
										<i class="icon iconfont"
										   :class="item.type ? 'icon-' + item.type.toLowerCase() : 'icon-datasource-o'"></i>
									</div>
									<div class="ds-name">{{item.name}}</div>
									<div class="ds-time">{{item.createTime.substring(0, 10)}}</div>
								</div>
								<div class="ds-opts">
									<div class="ds-opts-btn">
										<i class="icon iconfont icon-edit"
										   v-if="!isFileSource(item)"
										   @click="editDataSource(item)"></i>
										<i class="icon iconfont icon-delete"
										   @click="deleteDataSource(item)"></i>
									</div>
								</div>
							</div>
						</el-col>
					</template>
				</template>
			</div>
		</div>
		<Importer :config="importer"></Importer>
		<DataSourceEdit v-if="edit.visible" :config="edit">
		</DataSourceEdit>
	</div>
</template>

<script>
	import '../assets/styles/screen/overview/main.scss'
	import DataSourceEdit from '@/components/datasource/DataSourceEdit'
	import Importer from '@/components/overview/Importer'
	import API from '../config/api'
	import CONST from '../config/const'

	export default {
		name: "Overview",
		data() {
			return {
				menuIndex: '0',
				screen: {
					index: null,
					list: null
				},
				dataSource: {
					list: null
				},
				importer: {
					visible: false,
				},
				edit: {
					visible: false,
					dataSource: {}
				}
			}
		},
		mounted() {
			this.listScreen()
			this.listDataSource()
		},
		methods: {
			/**
			 * 切换菜单
			 */
			selectMenu(index) {
				this.menuIndex = index
			},
			/**
			 * 可视化列表
			 */
			listScreen() {
				this.ajax({
					url: API.SCREEN.LIST,
					method: CONST.AJAX.POST,
					data: {}
				}).then(response => {
					this.screen.list = response.data
				})
			},
			/**
			 * 数据源列表
			 */
			listDataSource() {
				this.ajax({
					url: API.DATA_SOURCE.LIST_BY_USER,
					method: CONST.AJAX.POST,
				}).then(response => {
					this.dataSource.list = response.data
				})
			},
			/**
			 * 新增
			 */
			addScreen() {
				this.$router.push('./design')
			},
			/**
			 * 编辑
			 */
			editScreen(index) {
				this.$router.push('./design?id=' + this.screen.list[index].id)
			},
			/**
			 * 删除
			 */
			deleteScreen(index) {
				this.$confirm('可视化成果删除后无法恢复, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.ajax({
						url: API.SCREEN.DELETE,
						params: {id: this.screen.list[index].id},
					}).then(response => {
						this.screen.list.splice(index, 1)
						this.$message({
							message: response['message'],
							type: 'success'
						})
					})
				}).catch(() => {
				})
			},
			/**
			 * 导出
			 */
			exportScreen(index) {
				let config = this.formatConfigParam(this.screen.list[index])
				config.id = null
				let aTag = document.createElement('a')
				let blob = new Blob([JSON.stringify(config, null, 4)])
				aTag.download = "config-" + new Date().getTime()
				aTag.href = URL.createObjectURL(blob)
				aTag.click()
				URL.revokeObjectURL(blob)
			},
			/**
			 * 预览
			 */
			previewScreen(index) {
				let config = this.formatConfigParam(this.screen.list[index])
				this.ajax({
					url: API.SCREEN.PREVIEW,
					method: CONST.AJAX.POST,
					data: config,
				}).then(response => {
					let link = this.$router.resolve({
						path: './presentation',
						query: {id: response.data.id}
					})
					window.open(link.href, '_blank')
				})
			},
			/**
			 * 发布
			 */
			publishScreen(index) {
				let config = this.formatConfigParam(this.screen.list[index])
				this.ajax({
					url: API.SCREEN.PUBLISH,
					method: CONST.AJAX.POST,
					data: config,
				}).then(response => {
					if (!this.config.id) {
						this.config.id = response.data.id
					}
					this.$message(CONST.MESSAGE.SAVE_SUCCESS)
				})
			},
			/**
			 * 将可视化配置转为参数
			 */
			formatConfigParam(config) {
				let param = _.cloneDeep(config)
				for (let key in param) {
					let value = param[key]
					if (_.isObject(value)) {
						param[key] = JSON.stringify(value)
					}
				}
				return param
			},
			/**
			 * 新增数据源
			 */
			addDataSource() {
				this.edit = {
					visible: true,
					dataSource: null
				}
			},
			/**
			 * 编辑数据源
			 */
			editDataSource(dataSource) {
				this.ajax({
					url: API.DATA_SOURCE.GET,
					method: CONST.AJAX.POST,
					data: {dsId: dataSource.id, type: dataSource.type},
				}).then(response => {
					this.edit = {
						visible: true,
						dataSource: response.data
					}
				})

			},
			/**
			 * 删除数据源
			 */
			deleteDataSource(dataSource) {
				this.$confirm('数据源删除后无法恢复, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.ajax({
						url: API.DATA_SOURCE.DELETE,
						method: CONST.AJAX.POST,
						data: {dsId: dataSource.id, type: dataSource.type},
					}).then(response => {
						this.$message(CONST.MESSAGE.DELETE_SUCCESS)
						for (let i = 0; i < this.dataSource.list.length; i++) {
							if (this.dataSource.list[i].id === dataSource.id) {
								this.dataSource.list.splice(i, 1)
							}
						}
					})
				}).catch(() => {

				})
			},
			/**
			 * 判断是否是文件数据源
			 */
			isFileSource(dataSource) {
				if (dataSource.type === 'EXCEL') {
					return true
				}
				return false
			},
			/**
			 * 显示导入弹窗
			 */
			showImporter(e) {
				this.importer.visible = true
				e.stopPropagation()
			}
		},
		watch: {
			'importer.visible': function (val) {
				if (!this.importer.visible) {
					this.listDataSource()
				}
			},
			'edit.visible': function (val) {
				if (!this.edit.visible) {
					this.listDataSource()
				}
			},
		},
		components: {
			'Importer': Importer,
			'DataSourceEdit': DataSourceEdit
		}
	}
</script>
