<template>
	<Table :config="config"></Table>
</template>

<script>
	import Table from '@/components/screen/layer/base/Table'
	import CONST from '../../../../config/const'
	import _ from 'lodash'

	export default {
		name: 'BaseTable',
		props: ['config'],
		data() {
			return {}
		},
		created() {
			this.initStyle()
			this.initOption()
			this.initData()
		},
		mounted() {

		},
		beforeDestroy() {
			// 销毁前注销组件的数据绑定
			this.$store.commit(CONST.MUTATIONS.UNREGISTER, {
				comp: this
			})
		},
		methods: {
			/**
			 * 初始化样式，对新组件使用默认样式
			 */
			initStyle() {
				let style = _.assignIn({}, {
					top: 30,
					left: 30,
					opacity: 1,
					display: 'block',
					backgroundColor: 'rgba(0,0,0,0)',
					borderWidth: 2,
					borderColor: '#00BAFF',
					header: {
						fontSize: 14,
						color: '#cdcdcd',
						height: 32,
						backgroundColor: '#151a23',
						borderWidth: 1,
						borderColor: '#00BAFF',
						paddingLeft: 0,
						paddingTop: 10,
					},
					body: {
						fontSize: 14,
						color: '#cdcdcd',
						height: 32,
						backgroundColor: '#151a23',
						borderWidth: 1,
						borderColor: '#00BAFF',
						paddingLeft: 10,
						paddingTop: 10,
					}
				}, this.config.style)
				this.config.style = style
			},
			/**
			 * 声明默认option
			 */
			initOption() {
				this.config.option = _.assignIn({}, {
					border: true,
					stripe: true,
					fit: false,
					size: "medium",
					maxHeight: null,
					highlightCurrentRow: true,
					showSummary: false,
					sumText: '合计',
					columns: [{
						type: 'text', text: '名称', prop: 'name', width: 100, fixed: false, sortable: true
					}, {
						type: 'text', text: '值', prop: 'value', width: 100, fixed: false, sortable: true
					}]
				}, this.config.option)
			},
			/**
			 * 初始化数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: "中国", value: 300},
					{name: "美国", value: 200},
					{name: "俄罗斯", value: 100}
				]
			},
		},
		watch: {},
		components: {
			"Table": Table
		}
	}
</script>
