<template>
	<Plugin :config="config">
		<el-progress :style="calcStyle()"
		             :stroke-width="config.option.width"
		             type="circle"
		             :show-text="config.option.showText"
		             :percentage="(config.option.data[0] && config.option.data[0].value) || 0"
		             :color="config.option.color"
		             @click="onEvent('click', data[0])"></el-progress>
	</Plugin>
</template>

<script>
	import '../../../../assets/styles/screen/common/paint/scroll-text.scss'
	import Plugin from '@/components/screen/layer/base/Plugin'
	import _ from 'lodash'

	export default {
		name: 'ProgressCircle',
		props: ['config'],
		data() {
			return {}
		},
		created() {
			this.initStyle()
			this.initData()
			this.initOption()
			this.initEvent()
			this.initProxy()
			this.initTransform()
		},
		methods: {
			/**
			 * 计算样式
			 */
			calcStyle() {
				return {
					width: '100%',
					height: '100%',
					color: this.config.style.color,
					opacity: this.config.style.opacity,
					fontSize: this.config.style.fontSize + 'px',
				}
			},
			/**
			 * 初始化样式
			 */
			initStyle() {
				let style = _.assignIn({}, {
					width: 200,
					height: 200,
					opacity: 1,
					color: '#ffffff',
					fontSize: 20
				}, this.config.style)
				this.config.style = style
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{value: 80},
				]
			},
			/**
			 * 声明默认option
			 */
			initOption() {
				this.config.option = this.config.option || {
					color: '#00BAFF',
					width: 12,
					showText: true,
					data: {value: 0}
				}
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [{
					name: 'click',
					comment: '点击事件',
					binders: [
						{name: 'value', comment: '值', prop: 'value', state: null},
					]
				}]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = _.assignIn({}, {
					type: 1,
				}, this.config.proxy)
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						value: null,
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let data = this.config.data
				let mapper = this.config.transform.mapper
				let propValue = mapper.value || 'value'
				let optionData = []
				for (let i = 0; i < data.length; i++) {
					optionData.push({
						value: data[i][propValue],
					})
				}
				this.config.option.data = optionData
			},
			/**
			 * 触发事件代理
			 */
			onEvent(eventName, item) {
				this.$children[0].onEvent(eventName, item)
			},
		},
		components: {
			'Plugin': Plugin
		},
	}
</script>
