<template>
	<Plugin :config="config">
		<div class="scroll-text">
			<transition-group tag="ul" :style="calcStyle()" :name="config.option.type">
				<li v-for="(item,i) in config.option.data"
				    v-show="i == index"
				    :key='i'
				    @click="onEvent('click', item)">
					<div>{{item.name}}</div>
				</li>
			</transition-group>
		</div>
	</Plugin>
</template>

<script>
	import '../../../../assets/styles/screen/common/paint/scroll-text.scss'
	import Plugin from '@/components/screen/layer/base/Plugin'
	import _ from 'lodash'

	export default {
		name: 'ScrollText',
		props: ['config'],
		data() {
			return {
				// 当前索引
				index: 0,
				// 播放线程
				thread: null,
				// 原配置参数
				oldType: 'scroll-up',
			}
		},
		created() {
			this.initStyle()
			this.initData()
			this.initOption()
			this.initEvent()
			this.initProxy()
			this.initTransform()
		},
		methods: {
			/**
			 * 计算字幕样式
			 */
			calcStyle() {
				return {
					width: this.config.style.width + 'px',
					height: this.config.style.height + 'px',
					lineHeight: this.config.style.height + 'px',
					opacity: this.config.style.opacity,
					display: this.config.style.display,
					color: this.config.style.color,
					fontSize: this.config.style.fontSize + 'px',
				}
			},
			/**
			 * 初始化样式
			 */
			initStyle() {
				let style = _.assignIn({}, {
					width: 200,
					height: 40,
					opacity: 1,
					color: '#00BAFF',
					fontSize: 20
				}, this.config.style)
				this.config.style = style
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: '消息一', value: 0},
					{name: '消息二', value: 1},
					{name: '消息三', value: 2},
				]
			},
			/**
			 * 声明默认option
			 */
			initOption() {
				this.config.option = this.config.option || {
					type: 'scroll-up',
					delayTime: 2,
					data: []
				}
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [{
					name: 'click',
					comment: '点击事件',
					binders: [
						{name: 'name', comment: '文本', prop: 'name', state: null},
						{name: 'value', comment: '值', prop: 'value', state: null},
					]
				}]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = _.assignIn({}, {
					type: 1,
				}, this.config.proxy)
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						name: null,
						value: null,
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let data = this.config.data
				let mapper = this.config.transform.mapper
				let propName = mapper.name || 'name'
				let propValue = mapper.value || 'value'
				let optionData = []
				for (let i = 0; i < data.length; i++) {
					optionData.push({
						name: data[i][propName],
						value: data[i][propValue],
					})
				}
				this.config.option.data = optionData
				this.play()
			},
			/**
			 * 触发事件代理
			 */
			onEvent(eventName, item) {
				this.$children[0].onEvent(eventName, item)
			},
			/**
			 * 循环滚动
			 */
			play() {
				this.thread = setInterval(() => {
					if (this.config.option.type !== this.oldType) {
						switch (this.config.option.type) {
							// 其中线性滚动的时间间隔，必须与样式文件中设置的动画时间一致
							case 'scroll-up':
								this.config.option.delayTime = 2
								break
							case 'scroll-up-linear':
								this.config.option.delayTime = 2
								break
							case 'scroll-left':
								this.config.option.delayTime = 2
								break
							case 'scroll-left-linear':
								this.config.option.delayTime = 4
								break
							default:
								this.config.option.delayTime = 2
								break
						}
						this.oldType = this.config.option.type
						this.stop()
						this.play()
					}
					this.getText()
				}, this.config.option.delayTime * 1000)
			},
			/**
			 * 获取文本
			 */
			getText() {
				if (!this.config.option.data || !this.config.option.data.length) {
					return
				}
				if (this.index === this.config.option.data.length - 1) {
					this.index = 0
				} else {
					this.index++
				}
			},
			/**
			 * 停止
			 */
			stop() {
				clearInterval(this.thread)
				this.index = 0
			}
		},
		destroyed() {
			this.stop()
		},
		components: {
			'Plugin': Plugin
		},
	}
</script>
