<template>
	<Chart :config="config"></Chart>
</template>

<script>
	import Chart from '@/components/screen/layer/base/Chart'
	import _ from 'lodash'

	export default {
		name: 'BaseTree',
		props: ['config'],
		data() {
			return {}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: 'rgba(0,0,0,0)',
					tooltip: {
						show: true,
						trigger: "item",
						backgroundColor: '#2F3943',
						borderColor: 'rgba(0,0,0,0)',
						borderWidth: 1,
						padding: [5, 10, 5, 10],
						textStyle: {
							fontSize: 12,
							color: '#ffffff',
						},
						extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)',
					},
					series: [{
						type: 'tree',
						name: '',
						data: null,
						top: 15,
						left: 50,
						bottom: 15,
						right: 150,
						symbolSize: 7,
						orient: 'LR',
						label: {
							normal: {
								position: 'left',
								verticalAlign: 'middle',
								align: 'right',
								rotate: 0,
								textStyle: {
									fontSize: 14,
									color: '#ffffff'
								}
							}
						},
						leaves: {
							label: {
								normal: {
									position: 'right',
									verticalAlign: 'middle',
									rotate: 0,
									align: 'left',
									textStyle: {
										fontSize: 14,
										color: '#ffffff'
									}
								}
							}
						},
						expandAndCollapse: true,
						animationDuration: 550,
						animationDurationUpdate: 750
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{"id": 0, "pid": null, "name": "flare", "value": 2105},
					{"id": 1, "pid": 0, "name": "flex", "value": 2105},
					{"id": 2, "pid": 0, "name": "scale", "value": 2105},
					{"id": 3, "pid": 0, "name": "display", "value": 2105},
					{"id": 4, "pid": 2, "name": "IScaleMap", "value": 2105},
					{"id": 5, "pid": 2, "name": "LinearScale", "value": 1316},
					{"id": 6, "pid": 2, "name": "LogScale", "value": 3151},
					{"id": 7, "pid": 2, "name": "OrdinalScale", "value": 3770},
					{"id": 8, "pid": 2, "name": "QuantileScale", "value": 2435},
					{"id": 9, "pid": 2, "name": "QuantitativeScale", "value": 4839},
					{"id": 10, "pid": 2, "name": "RootScale", "value": 1756},
					{"id": 11, "pid": 2, "name": "Scale", "value": 4268},
					{"id": 12, "pid": 2, "name": "ScaleType", "value": 1821},
					{"id": 13, "pid": 2, "name": "TimeScale", "value": 5833},
					{"id": 14, "pid": 3, "name": "DirtySprite", "value": 8833}
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [
					{
						name: 'click',
						comment: '点击事件',
						binders: [
							{name: 'id', comment: 'ID', prop: 'id', state: null},
							{name: 'pid', comment: '父节点ID', prop: 'pid', state: null},
							{name: 'name', comment: '数据名', prop: 'name', state: null},
							{name: 'value', comment: '数据值', prop: 'value', state: null},
						]
					}
				]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						id: null,
						pid: null,
						name: null,
						value: null,
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let idProp = this.config.transform.mapper.id || "id"
				let pidProp = this.config.transform.mapper.pid || "pid"
				let nameProp = this.config.transform.mapper.name || "name"
				let valueProp = this.config.transform.mapper.value || "value"
				if (!this.config.data || !this.config.data.length) {
					return
				}
				let map = {}
				for (let i = 0; i < this.config.data.length; i++) {
					let item = this.config.data[i]
					map[item[idProp]] = {
						id: item[idProp],
						pid: item[pidProp],
						name: item[nameProp],
						value: item[valueProp],
						children: [],
					}
				}
				let rootNodes = []
				for (let key in map) {
					let item = map[key]
					if (item.pid === undefined || item.pid === null) {
						rootNodes.push(item)
					} else {
						map[item.pid]['children'].push(item)
					}
				}
				for (let i = 0; i < this.config.option.series.length; i++) {
					this.config.option.series[i].data = [rootNodes[i]]
				}
			}
		},
		components: {
			'Chart': Chart
		},
	}
</script>
