<template>
	<div class="screen-view"
	     :style="calcViewStyle"
	     v-if="config && config.sequence && config.sequence.length">
		<template v-for="(id, index) in config.sequence">
			<div class="view-box"
			     :style="calcCompBoxStyle(id)">
				<component :is="loader[id].default"
				           :config="config.components[id].config"></component>
			</div>
		</template>
	</div>
</template>

<script>
	import '../assets/styles/screen/presentation/main.scss'
	import API from '../config/api'

	export default {
		name: 'Presentation',
		data() {
			return {
				loader: {},
				config: null
			}
		},
		created() {
			this.init()
		},
		computed: {
			/**
			 * 计算画布样式
			 * @returns
			 */
			calcViewStyle() {
				if (!this.config) {
					return
				}
				return {
					width: this.config.base.size.width + 'px',
					height: this.config.base.size.height + 'px',
					backgroundColor: this.config.base.backgroundColor,
					backgroundImage: this.config.base.backgroundImage ?
							'url(' + this.config.base.backgroundImage + ')' : 'none',
					backgroundSize: this.calcBgImageSize
				}
			},
			/**
			 * 根据配置项调整背景图片尺寸
			 */
			calcBgImageSize() {
				switch (this.config.base.scaleMode) {
					case 1:
						return 'auto 100%'
					case 2:
						return '100% 100%'
					default:
						return '100% auto'
				}
			}
		},
		methods: {
			init() {
				let id = this.$route.query.id
				if (!id) {
					this.$message.error('错误：未找到可视化。')
					return
				}
				this.ajax({
					url: API.SCREEN.GET_BY_ID,
					params: {id: id},
				}).then(response => {
					let config = response.data
					if (!config) {
						this.$message.error('错误：未找到可视化。')
					}
					config.base = JSON.parse(config.base)
					config.components = JSON.parse(config.components)
					config.sequence = JSON.parse(config.sequence)
					this.config = config
				})
			},
			/**
			 * 计算组件外框样式
			 */
			calcCompBoxStyle(id) {
				if (!this.config) {
					return
				}
				let style = this.config.components[id].config.style

				return {
					zIndex: this.getIndexById(id),
					top: style && style.top ? style.top + 'px' : 0,
					left: style && style.left ? style.left + 'px' : 0,
				}
			},
			/**
			 * 动态加载组件
			 */
			loadComponent() {
				if (!this.config || !this.config.sequence || !this.config.sequence.length) {
					return
				}
				for (let i = 0; i < this.config.sequence.length; i++) {
					let id = this.config.sequence[i]
					let comp = this.config.components[id]
					if (!comp || !comp.name || this.loader[comp.config.id]) {
						continue
					}
					let path = 'components/screen/layer/' + comp.category + '/' + comp.name
					this.loader[comp.config.id] = (require('../../' + path + '.vue'))
				}
			},
			/**
			 * 根据id获取组件索引
			 */
			getIndexById(itemId) {
				for (let i = 0; i < this.config.sequence.length; i++) {
					let id = this.config.sequence[i]
					if (id == itemId) {
						return i
					}
				}
			}
		},
		watch: {
			config: {
				deep: true,
				immediate: true,
				handler: function () {
					this.loadComponent()
				}
			}
		}
	}
</script>

