<template>
	<el-collapse value="0" accordion>
		<el-collapse-item title="基础属性" name="0">
			<el-row>
				<el-col :span="8" class="tag">位置</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.left"
					                 size="mini"
					                 controls-position="right">
					</el-input-number>
					<div class="description">横坐标</div>
				</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.top"
					                 size="mini"
					                 controls-position="right">
					</el-input-number>
					<div class="description">纵坐标</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">其他</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.opacity"
					                 size="mini"
					                 controls-position="right" :min="0" :max="1" :step="0.05">
					</el-input-number>
					<div class="description">透明度</div>
				</el-col>
				<el-col :span="8">
				</el-col>
			</el-row>
		</el-collapse-item>
		<el-collapse-item title="样式" name="1">
			<el-row>
				<el-col :span="8" class="tag">方向</el-col>
				<el-col :span="8">
					<el-checkbox v-model="component.config.option.vertical">
						{{component.config.option.vertical ? "纵向" : "横向"}}
					</el-checkbox>
				</el-col>
			</el-row>
			<el-row v-if="component.config.option.vertical">
				<el-col :span="8" class="tag">高度</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.progressHeight"
					                 size="mini"
					                 controls-position="right" :min="0" :step="1">
					</el-input-number>
				</el-col>
			</el-row>
			<el-row v-else>
				<el-col :span="8" class="tag">宽度</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.progressWidth"
					                 size="mini"
					                 controls-position="right" :min="0" :step="1">
					</el-input-number>
				</el-col>
			</el-row>
		</el-collapse-item>
		<el-collapse-item title="功能" name="2">
			<el-row>
				<el-col :span="8" class="tag">初始位置</el-col>
				<el-col :span="8">
					<el-select v-model="component.config.option.start"
					           placeholder="请选择"
					           size="mini" @change="changeStart">
						<el-option v-for="item in nodeList"
						           :key="item.value"
						           :label="item.label"
						           :value="item.value">
						</el-option>
					</el-select>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">允许播放</el-col>
				<el-col :span="4">
					<el-checkbox v-model="component.config.option.playable"></el-checkbox>
				</el-col>
			</el-row>
			<el-row v-if="component.config.option.playable">
				<el-col :span="8" class="tag">时间间隔</el-col>
				<el-col :span="10">
					<el-input-number v-model="component.config.option.interval"
					                 size="mini"
					                 controls-position="right" :min="0" :step="1">
					</el-input-number>
				</el-col>
				<el-col :span="2" class="tag">
					<el-checkbox v-model="component.config.option.loop"></el-checkbox>
				</el-col>
				<el-col :span="4" class="tag">循环</el-col>
			</el-row>
		</el-collapse-item>
	</el-collapse>
</template>

<script>
	export default {
		name: 'Timeline',
		props: ['component'],
		data() {
			return {
				nodeList: [],
			}
		},
		mounted() {
			this.initNodeList()
		},
		computed: {},
		methods: {
			/**
			 * 初始化【初始位置选项】
			 */
			initNodeList() {
				this.nodeList = []
				let data = this.component.config.option.data
				if (!data || !data.length) {
					return
				}
				for (let i = 0; i < data.length; i++) {
					this.nodeList.push({
						label: data[i].name,
						value: data[i].value,
					})
				}
			},
			/**
			 * 修改当前节点位置
			 * @param val
			 */
			changeStart(val) {
				this.component.config.option.value = val
			}
		},
		watch: {
			"component.config.option.data": {
				deep: true,
				handler: function () {
					this.initNodeList()
				}
			}
		},
		components: {}
	}
</script>
