<template>
	<Chart :config="config"></Chart>
</template>

<script>
	import Chart from '@/components/screen/layer/base/Chart'
	import _ from 'lodash'

	export default {
		name: 'StackBar',
		props: ['config'],
		data() {
			return {}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: 'rgba(0,0,0,0)',
					dataset: {
						dimensions: null,
						source: null,
					},
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'shadow'
						},
						backgroundColor: '#2F3943',
						borderColor: 'rgba(0,0,0,0)',
						borderWidth: 1,
						padding: [5, 10, 5, 10],
						textStyle: {
							fontSize: 14,
							color: '#ffffff',
						},
						extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)'
					},
					legend: {
						data: ['默认', '系列1'],
						orient: 'horizontal',
						top: 'top',
						left: 'center',
						itemWidth: 18,
						itemHeight: 8,
						textStyle: {
							fontSize: 12,
							color: ['#D4DFF5']
						},
					},
					xAxis: {
						type: 'category',
						axisLine: {
							show: true,
							lineStyle: {
								color: "#737985",
								width: 1,
							}
						},
						axisTick: {
							show: false,
						},
						axisLabel: {
							show: true,
							textStyle: {
								fontSize: 14,
								color: "#dddddd",
							}
						},
						splitLine: {
							show: false,
							lineStyle: {
								width: 1,
								color: ['#737985']
							}
						},
					},
					yAxis: [{
						type: 'value',
						axisLabel: {
							show: true,
							margin: 10,
							textStyle: {
								fontSize: 14,
								color: '#dddddd'
							}
						},
						axisTick: {
							show: false,
						},
						axisLine: {
							show: true,
							lineStyle: {
								color: "#737985",
								width: 1,
							},
						},
						splitLine: {
							show: false,
							lineStyle: {
								color: ['#737985']
							}
						},
					}],
					series: [{
						name: '默认',
						type: 'bar',
						barWidth: 20,
						barGap: 1,
						stack: '0',
						itemStyle: {
							normal: {
								color: {
									x: 0,
									y: 0,
									x2: 0,
									y2: 1,
									type: "linear",
									global: false,
									colorStops: [{
										offset: 0,
										color: "#00BBFF"
									}, {
										offset: 1,
										color: "#00BBFF"
									}]
								},
							}
						}
					}, {
						name: '系列1',
						type: 'bar',
						barWidth: 20,
						barGap: 1,
						stack: '0',
						itemStyle: {
							normal: {
								color: {
									x: 0,
									y: 0,
									x2: 0,
									y2: 1,
									type: "linear",
									global: false,
									colorStops: [{
										offset: 0,
										color: "#3CE7C9"
									}, {
										offset: 1,
										color: "#3CE7C9"
									}]
								},
							}
						}
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: '一月', value: 80, series1: 60, series2: 60},
					{name: '二月', value: 87, series1: 55, series2: 60},
					{name: '三月', value: 51, series1: 35, series2: 60},
					{name: '四月', value: 81, series1: 68, series2: 60},
					{name: '五月', value: 23, series1: 84, series2: 60},
					{name: '六月', value: 45, series1: 26, series2: 60},
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [
					{
						name: 'click',
						comment: '点击事件',
						binders: [
							{name: 'name', comment: '数据名', prop: 'name', state: null},
							{name: 'value', comment: '系列值0', prop: 'value', state: null},
						]
					}
				]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						name: null,
						value: null,
						series1: null
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let dimensions = []
				for (let key in this.config.transform.mapper) {
					let dim = this.config.transform.mapper[key] || key
					if (!this.config.data[0].hasOwnProperty(dim)) {
						dim = null
					}
					dimensions.push(dim)
				}
				this.config.option.dataset = {
					dimensions: dimensions,
					source: this.config.data
				}
			}
		},
		components: {
			'Chart': Chart
		},
	}
</script>
