<template>
	<div class="config" @click="stopPropagation($event)">
		<Screen v-show="state.index===undefined || state.index===null"
		        :config="config" :state="state"/>
		<Base v-show="state.index!==undefined && state.index!==null"
		      :config="config"
		      :state="state"/>
	</div>
</template>

<script>
	import Screen from "./config/Screen"
	import Base from "./config/Base"

	export default {
		name: "Config",
		props: ["config", "state"],
		data() {
			return {}
		},
		methods: {
			/**
			 * config面板阻止点击冒泡，防止点击后组件被取消选中
			 * @param e
			 */
			stopPropagation(e) {
				e.stopPropagation()
			}
		},
		components: {
			'Screen': Screen,
			'Base': Base
		}
	}
</script>

