<template>
	<Plugin :config="config">
		<div class="scroll-list">
			<transition-group tag="ul" :style="calcStyle()" name="scroll-up">
				<li v-for="(item,i) in config.option.data"
				    v-show="isVisible(i)"
				    :style="calcItemStyle()"
				    :key='i'
				    @click="onEvent('click', item)">
					<div>{{item.name}}</div>
				</li>
			</transition-group>
		</div>
	</Plugin>
</template>

<script>
	import '../../../../assets/styles/screen/common/paint/scroll-list.scss'
	import Plugin from '@/components/screen/layer/base/Plugin'
	import _ from 'lodash'

	export default {
		name: 'ScrollText',
		props: ['config'],
		data() {
			return {
				// 播放线程
				thread: null,
				// 当前索引
				index: 0,
				// 原间隔时间
				oldDelay: 1
			}
		},
		created() {
			this.initStyle()
			this.initData()
			this.initOption()
			this.initEvent()
			this.initProxy()
			this.initTransform()
		},
		methods: {
			/**
			 * 是否隐藏，在列表显示范围中则显示
			 */
			isVisible(i) {
				if (i >= this.index && i < this.index + this.config.option.count) {
					return true
				}
				return false
			},
			/**
			 * 计算整体样式
			 */
			calcStyle() {
				return {
					height: this.config.style.itemHeight * this.config.option.count + 'px',
					opacity: this.config.style.opacity,
					color: this.config.style.color,
					fontSize: this.config.style.fontSize + 'px',
				}
			},
			/**
			 * 计算单行样式
			 */
			calcItemStyle() {
				return {
					width: this.config.style.itemWidth + 'px',
					height: this.config.style.itemHeight + 'px',
					lineHeight: this.config.style.itemHeight + 'px',
				}
			},
			/**
			 * 初始化样式
			 */
			initStyle() {
				let style = _.assignIn({}, {
					itemWidth: 200,
					itemHeight: 40,
					opacity: 1,
					color: '#00BAFF',
					fontSize: 20
				}, this.config.style)
				this.config.style = style
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: '消息一', value: 0},
					{name: '消息二', value: 1},
					{name: '消息三', value: 2},
					{name: '消息四', value: 3},
					{name: '消息五', value: 3},
					{name: '消息六', value: 3},
					{name: '消息七', value: 3},
					{name: '消息八', value: 3},
				]
			},
			/**
			 * 声明默认option
			 */
			initOption() {
				this.config.option = this.config.option || {
					count: 3,
					delayTime: 5,
					data: []
				}
				this.oldDelay = this.config.option.delayTime
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [{
					name: 'click',
					comment: '点击事件',
					binders: [
						{name: 'name', comment: '文本', prop: 'name', state: null},
						{name: 'value', comment: '值', prop: 'value', state: null},
					]
				}]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = _.assignIn({}, {
					type: 1,
				}, this.config.proxy)
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						name: null,
						value: null,
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let data = this.config.data
				let mapper = this.config.transform.mapper
				let propName = mapper.name || 'name'
				let propValue = mapper.value || 'value'
				let optionData = []
				for (let i = 0; i < data.length; i++) {
					optionData.push({
						name: data[i][propName],
						value: data[i][propValue],
					})
				}
				this.config.option.data = optionData
				this.play()
			},
			/**
			 * 触发事件代理
			 */
			onEvent(eventName, item) {
				this.$children[0].onEvent(eventName, item)
			},
			/**
			 * 循环滚动
			 */
			play() {
				this.thread = setInterval(() => {
					if (this.oldDelay !== this.config.option.delayTime) {
						this.stop()
						this.play()
					}
					this.scrollList()
				}, this.config.option.delayTime * 1000)
			},
			/**
			 * 获取文本
			 */
			scrollList() {
				if (!this.config.option.data || !this.config.option.data.length || this.config.option.data.length <= this.config.option.count) {
					return
				}
				this.index += this.config.option.count
				if (this.index >= this.config.option.data.length) {
					this.index = 0
				}
			},
			/**
			 * 停止
			 */
			stop() {
				clearInterval(this.thread)
			}
		},
		destroyed() {
			this.stop()
		},
		components: {
			'Plugin': Plugin
		},
	}
</script>
