<template>
	<Chart :config="config"></Chart>
</template>

<script>
	import Chart from '@/components/screen/layer/base/Chart'
	import _ from 'lodash'

	export default {
		name: 'BaseRadar',
		props: ['config'],
		data() {
			return {
				seriesOption: [],
				dataset: []
			}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: 'transparent',
					tooltip: {
						show: true,
						trigger: "item",
						backgroundColor: '#2F3943',
						borderColor: 'transparent',
						borderWidth: 1,
						padding: [5, 10, 5, 10],
						textStyle: {
							color: '#ffffff',
						},
						extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)',
					},
					legend: {
						show: true,
						icon: "circle",
						top: 'top',
						left: 'left',
						orient: "horizontal",
						textStyle: {
							fontSize: 14,
							color: "#fff"
						},
						data: ["默认"]
					},
					radar: {
						// 半径
						radius: 100,
						// 起始角度
						startAngle: 90,
						// 轴分割段数
						splitNumber: 4,
						// polygon / circle
						shape: "circle",
						// 坐标文本
						name: {
							show: true,
							fontSize: 14,
							color: "#dedede",
							fontWeight: "normal"
						},
						// 分隔区背景色
						splitArea: {
							show: true,
							areaStyle: {
								color: ["rgba(0,0,0,0)"]
							}
						},
						// 坐标轴线
						axisLine: {
							show: true,
							lineStyle: {
								color: "#acacac"
							}
						},
						// 分隔线
						splitLine: {
							show: true,
							lineStyle: {
								color: "#777777"
							}
						},
						indicator: null
					},
					series: [{
						name: "默认",
						type: "radar",
						symbol: "circle",
						symbolSize: 10,
						areaStyle: {
							normal: {
								color: "rgba(245, 166, 35, 0.4)"
							}
						},
						itemStyle: {
							color: 'rgba(245, 166, 35, 1)',
							borderColor: 'rgba(245, 166, 35, 0.3)',
							borderWidth: 10,
						},
						lineStyle: {
							normal: {
								type: "dashed",
								color: "rgba(245, 166, 35, 1)",
								width: 2
							}
						},
						data: null
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{series: "张三", name: "语文", value: 55, max: 100},
					{series: "张三", name: "数学", value: 67, max: 100},
					{series: "张三", name: "英语", value: 89, max: 100},
					{series: "张三", name: "物理", value: 78, max: 100},
					{series: "张三", name: "化学", value: 78, max: 100},
					{series: "张三", name: "生物", value: 67, max: 100},
					{series: "李四", name: "语文", value: 57, max: 100},
					{series: "李四", name: "数学", value: 85, max: 100},
					{series: "李四", name: "英语", value: 74, max: 100},
					{series: "李四", name: "物理", value: 85, max: 100},
					{series: "李四", name: "化学", value: 81, max: 100},
					{series: "李四", name: "生物", value: 62, max: 100}
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [
					{
						name: 'click',
						comment: '点击事件',
						binders: [
							{name: 'series', comment: '系列名', prop: 'series', state: null},
							{name: 'name', comment: '数据名', prop: 'name', state: null},
							{name: 'value', comment: '数据值', prop: 'value', state: null},
							{name: 'max', comment: '最大值', prop: 'max', state: null},
						]
					}
				]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						name: null,
						value: null,
						series: null,
						max: null
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				if (!this.config.data || !this.config.data.length) {
					return
				}
				let indicat = []
				let max = 0
				let nameProp = this.config.transform.mapper.name || 'name'
				let valueProp = this.config.transform.mapper.value || 'value'
				let seriesProp = this.config.transform.mapper.series || 'series'
				let maxProp = this.config.transform.mapper.max || 'max'
				let dataset = []
				// 判断是否有分series的属性，如果存在，则分成多个dataset
				if (this.config.data[0][seriesProp]) {
					this.seriesOption = []
					for (let i = 0; i < this.config.data.length; i++) {
						let item = this.config.data[i]
						let seriesValue = item[seriesProp]
						let datasetIndex = this.seriesOption.length
						let exist = false
						for (let j = 0; j < this.seriesOption.length; j++) {
							if (this.seriesOption[j] == seriesValue) {
								exist = true
								datasetIndex = j
								break
							}
						}
						if (!exist) {
							this.seriesOption.push(seriesValue)
							dataset.push([])
						}
						// 判断indicator是否已存在，如不存在，则添加到缓存
						let nameExist = false
						let nameVal = item[nameProp]
						for (let j = 0; j < indicat.length; j++) {
							if (indicat[j]['name'] == nameVal) {
								nameExist = true
							}
						}
						if (!nameExist) {
							indicat.push({
								name: nameVal,
								max: item[maxProp]
							})
						}
						dataset[datasetIndex].push(item[valueProp])
						if (item[valueProp] > max) {
							max = item[valueProp]
						}
					}
				} else {
					dataset = [[]]
					for (let i = 0; i < this.config.data.length; i++) {
						let item = this.config.data[i]
						dataset[0].push(item[valueProp])
						indicat.push({
							name: item[nameProp],
							max: item[maxProp]
						})
					}
				}
				this.dataset = dataset
				this.config.option.indicator = indicat
				// 将dataset的中的每个集合依次加载到series中
				if (!this.config.option.series || !this.config.option.series.length) {
					return
				}
				for (let i = 0; i < this.config.option.series.length; i++) {
					this.config.option.series[i].data = [this.dataset[i]]
				}
			}
		},
		watch: {
			'config.option.series': {
				handler: function (val, oldVal) {
					if (!oldVal) {
						return
					}
					this.handler()
				}
			}
		},
		components: {
			'Chart': Chart
		},
	}
</script>
