<template>
	<el-dialog width="50%"
	           title="导入配置"
	           :visible.sync="config.visible"
	           :close-on-click-modal="false">
		<el-row>
			<el-col :span="24">
				<el-input v-model="screen"
				          type="textarea"
				          :rows="12"
				          size="mini"
				          resize="none">
				</el-input>
			</el-col>
		</el-row>
		<div slot="footer" class="dialog-footer">
			<el-button size="mini" type="primary" @click="save">确 定</el-button>
			<el-button size="mini" @click="config.visible = false">取 消</el-button>
		</div>
	</el-dialog>
</template>

<script>
	import API from '../../config/api'
	import CONST from '../../config/const'

	export default {
		name: "Importer",
		props: ['config'],
		data() {
			return {
				rules: {
					screen: [
						{required: true, message: '配置不可为空', trigger: 'blur'}
					],
				},
				screen: null
			}
		},
		methods: {
			/**
			 * 保存数据源
			 */
			save() {
				let data = null
				try {
					data = JSON.parse(this.screen)
					this.ajax({
						url: API.SCREEN.SAVE,
						method: CONST.AJAX.POST,
						data: JSON.parse(this.screen),
					}).then(response => {
						if (response.success) {
							this.$message(CONST.MESSAGE.SAVE_SUCCESS)
							this.config.visible = false
						}
					})
				} catch (e) {
					this.$message({message: CONST.MESSAGE.JSON_PARSE_ERROR, type: 'error'})
				}
			},
		}
	}
</script>

