<!--
Chart组件，通过传入:config，生成相应类型的元素
配置项：
{
  1. 样式
  style: {
    // 是否隐藏，默认false
    hidden: false,
    class: 样式class
  }
  2. 数据绑定
  binder: {
    // name: 绑定规则命名
    // prop: 当前组件的属性路径，进行数据绑定的属性，必须在实例化后存在显式声明，
    // 例如：param.region，必须在config中配置 config: { param: {region: null} }
    // storeProp: Vuex中的属性路径
    // prop与storeProp进行单向数据绑定，当storeProp变化时，prop随之变化，
    // 并会触发当前组件的watch
    name:[prop, storeProp]
  },
  3. 报表配置
  option: {
    url: iframe引用页面的地址
  },
  4. 子页面消息代理
  handler: {
    // 由被引用页面向外发送的消息决定，
    // 例如：子页面发送click事件，则此处定义的click函数被触发
    // 函数返回值可包含如下属性，这些内容会被作为结果反馈给子页面
    // success: 函数执行结果，可省略，默认true
    // message: 成功或异常提示消息，可省略，默认为空
    // data: 执行成果数据，可省略，默认为空
  },
  5. 消息
  message: {
    // 存储需要发送消息，会生成消息队列依次执行
  }
}

消息对象
{
    id: 消息id,
    type: "send",
    parentId: 父组件id,
    action: 操作,
    data: 参数
}

结果对象
{
    id: 消息id,
    type: "back",
    parentId: 组件标识符,
    success: 操作是否成功,
    message: 成功或失败异常等提示消息,
    data: 结果数据
}

子页面规范
1. 在子页面所在工程中，创建message/message.js文件，内容如下
(function (global, factory) {
  if (typeof define === 'function' && define.amd) {
    define(factory);
  } else if (typeof exports === 'object') {
    module.exports = factory();
  } else {
    global.Message = factory();
  }
}(typeof window !== 'undefined' ? window : this, function () {

  var CONST_INIT_CHILD = 'initChild';

  var Message = function (config) {
    var _default = {
      /**
       * 消息栈
       */
      stack: {
        busy: false,
        queue: []
      },
      /**
       * 父组件的id和域
       */
      parentId: null,
      // parentDomain: null,
      listeners: {}
    };

    var me = Object.assign({}, _default, config);

    me.listeners.init = function (data) {
      // 获取父页面的信息
      me.parentId = data.parentId;
      // me.parentDomain = data.parentDomain;
        return {};
    };

    // 通知父页面将父页面信息返回给自己
    me.notificationParent = function(){
      me.post({
        action: CONST_INIT_CHILD
      });
    };

    /**
     * 代理
     */
    me.proxyMethod = function (context, methodName, param) {
      if (!context) {
        return
      }
      var func = context[methodName];
      if (func && typeof func === 'function') {
        return func.apply(me, param);
      }
    };

    /**
     * 发送消息
     */
    me.doPost = function () {
      // // 任务空闲并且队列中有消息时，发送消息
      // if (!me.parentDomain) {
      //     console.error('未定义父页面域')
      //     return
      // }
      if (!me.stack.busy && me.stack.queue.length) {
        me.stack.busy = true;
        window.parent.postMessage(JSON.stringify(me.stack.queue[0]), '*');
      }
    };

    /**
     * 添加消息
     */
    me.post = function (msg) {
      msg.id = 'msg-' + new Date().getTime();
      msg.type = 'send';
      msg.parentId = me.parentId;
      me.stack.queue.push(msg);
      me.doPost();
    };

    /**
     * 接受消息
     */
    me.receive = function (data) {
      if (!data) {
        return;
      }
      var msg = JSON.parse(data);
      if (!msg) {
        return;
      }
      if (msg.type.toLowerCase() === 'send') {
        me.receiveRequest(msg);
      } else {
        me.receiveResponse(msg);
      }
    };

    /**
     * 处理接受请求
     */
    me.receiveRequest = function (msg) {
      // 处理父页面发来的请求
      var result = {
        id: msg.id,
        type: 'back',
        parentId: me.parentId,
        success: true,
        message: '',
        data: null
      };
      var source = me.proxyMethod(me.listeners, msg.action, [msg.data]);
      result = Object.assign(result, source);
      // 将结果返回父页面
      window.parent.postMessage(JSON.stringify(result), '*');
    };

    /**
     * 处理接受反馈
     */
    me.receiveResponse = function (msg) {
      if (!msg.success) {
        var req = me.stack.queue[0];
        var err = '发送到父页面的请求【action: ' + req.action +
          ', data: ' + JSON.stringify(req.data) +
          '】出错：' + msg.message;
        console.error(err);
      }
      // 删除消息栈中第一个
      me.stack.queue.shift();
      me.stack.busy = false;
      me.doPost();
    };

    /**
     * 添加监听
     */
    window.addEventListener('message', function (e) {
      me.receive(e.data);
    }, false);

    me.notificationParent();

    return me;
  };

  return Message;
}));

2. 在被引用的子页面，引用message.js，引入方式支持AMD、CommonJS、UMD、原生JS规范，并增加如下代码：
var config = {
    listeners: {
        // 此处根据消息文档，对页面中可能接收到的消息，进行实现
        // 例如：当页面收到action: reload消息时，调用listeners.reload方法，
        // 并将消息的data属性值传入reload方法，作为参数
        reload: function(data) {
            // 根据消息中得到的data，对页面进行重载
        }
    }
}
var message = new Message(config);

3. 在需要向父页面发送消息的地方调用post方法，例如：
$(ele).on("click", function() {
    var msg = {
        action: "click",
        data: $(this).attr("data-index")
    }
    message.post(msg);
})


方法：
1. 修改Vuex Store的值
stateProp：vuex store中的属性，例如screen.govern.showCamera
value：给所有绑定的属性赋值
commit: function(stateProp, value)

2. 修改数据绑定的值
binderName：binder规则的name属性
value：给所有绑定的属性赋值
commitBinder: function(binderName, value)

3. reload: function()
重新加载报表

-->
<template>
	<div v-if="!style || !style.hidden"
	     class="chart-container"
	     :class="style.class"
	     :style="style.position"
	     :id="id + '-container'">
		<slot name="chart-before"></slot>
		<!-- 用于管理通用的报表容器样式 -->
		<iframe frameborder="0"
		        class="insight-ins"
		        :src="option.url"
		        :id="id"
		        :ref="id"></iframe>
		<slot name="chart-after"></slot>
	</div>
</template>

<script>
	import CONST from "../../../../config/const"

	export default {
		name: "Iframe",
		props: ['config'],
		data() {
			return {
				id: null,
				domain: null,
				style: this.config.style || {class: null, position: null},
				binder: this.config.binder,
				option: this.config.option,
				handler: {
					// 子页面通知父页面，父页面将信息返回给子页面
					initChild: function () {
						this.post({
							action: CONST.CORS_MESSAGE.ACTION.INIT,
							data: {
								parentId: this.id,
								parentDomain: this.domain
							}
						})
					}
				},
				iframe: null,
				iframeDomain: null,
				// 用于传入消息
				message: null,
				// 消息栈
				stack: {
					busy: false,
					queue: []
				},
			}
		},
		created() {
			if (!this.config) {
				throw new Error("Insight：未声明config属性")
			}
			this.id = this.config.id || "insight-" + new Date().getTime()
			this.domain = window.location.protocol + "//" + window.location.host
			this.handler = Object.assign(this.handler, this.config.handler)

			this.initPosition()
			this.initBinder()
		},
		mounted() {
			this.init()
		},
		beforeDestroy() {
			this.$store.commit(CONST.MUTATIONS.SCREEN.UNREGISTER, {
				comp: this
			})
		},
		methods: {
			/**
			 * 处理位置参数
			 */
			initPosition() {
				if (this.style.position) {
					let zIndex = this.style.position.zIndex || 0
					this.style.position = _.mapValues(this.style.position, function (o) {
						return o + "px"
					})
					_.set(this.style, 'position.zIndex', zIndex)
					_.set(this.style, 'position.position', 'absolute')
				}
			},
			/**
			 * 处理位置参数
			 */
			initBinder() {
				for (let key in this.binder) {
					let prop = this.binder[key][0]
					let storeProp = this.binder[key][1]
					this.$store.commit(CONST.MUTATIONS.SCREEN.REGISTER, {
						store: storeProp,
						prop: prop,
						comp: this
					})
				}
			},
			/**
			 * 初始化
			 */
			init() {
				this.iframe = this.$refs[this.id]
				let arr = this.option.url.split("//")
				let protocol = arr[0]
				let url = arr[1]
				let index = url.indexOf("/")
				if (index >= 0) {
					url = url.substring(0, index)
				}
				this.iframeDomain = protocol + "//" + url
				// 接收iframe内页面的消息
				let me = this
				window.addEventListener('message', function (e) {
					me.receive(e.data)
				}, false)
			},
			/**
			 * 处理接收到的消息
			 */
			receive(data) {
				if (!data) {
					return
				}
				let msg = typeof (data) === 'string' ? JSON.parse(data) : data
				if (!msg || !msg.id || !msg.type) {
					return
				}
				if (msg.type.toLowerCase() === CONST.CORS_MESSAGE.TYPE.SEND) {
					this.receiveRequest(msg)
				} else {
					this.receiveResponse(msg)
				}
			},
			/**
			 * 处理收到的请求
			 */
			receiveRequest(msg) {
				// 处理子页面发来的请求
				let result = {
					id: msg.id,
					type: CONST.CORS_MESSAGE.TYPE.BACK,
					parentId: this.parentId,
					success: true,
					message: "",
					data: null
				}
				// 调用handler中的方法，处理子页面发送来的请求
				let source = this.proxyMethod(this.handler, msg.action, [msg.data])
				result = Object.assign(result, source)
				// 将结果返回子页面
				this.iframe.contentWindow.postMessage(JSON.stringify(result), this.iframeDomain)
			},
			/**
			 * 处理收到的反馈
			 */
			receiveResponse(msg) {
				if (!msg.success) {
					let req = this.stack.queue[0]
					let err = "发送到子页面的请求【action: " + req.action +
							", data: " + JSON.stringify(req.data) +
							"】出错：" + msg.message
					console.error(err)
				}
				// 删除消息栈中第一个，并触发post
				this.stack.queue.shift()
				this.stack.busy = false
				this.doPost()
			},
			/**
			 * 修改binder的值
			 */
			commitBinder(binderKey, value) {
				this.commit(this.binder[binderKey][1], value)
			},
			/**
			 * 修改store的值
			 */
			commit(stateProp, value) {
				this.$store.commit(CONST.MUTATIONS.SCREEN.MODIFY, {
					store: stateProp,
					value: value
				})
			},
			/**
			 * 发送消息
			 */
			doPost() {
				// 任务空闲并且队列中有消息时，发送消息
				if (!this.stack.busy && this.stack.queue.length) {
					this.stack.busy = true
					this.iframe.contentWindow.postMessage(JSON.stringify(this.stack.queue[0]), this.iframeDomain)
				}
			},
			/**
			 * 添加消息
			 */
			post(msg) {
				msg.id = "msg-" + new Date().getTime()
				msg.type = CONST.CORS_MESSAGE.TYPE.SEND
				msg.parentId = this.parentId
				this.stack.queue.push(msg)
				this.doPost()
			},
			/**
			 * 代理
			 */
			proxyMethod(context, eventName, param) {
				if (!context) {
					return
				}
				let func = context[eventName]
				if (func && typeof func === "function") {
					return func.apply(this, param)
				}
			}
		},
		watch: {
			message: {
				deep: true,
				handler: function (val) {
					this.post(val)
				}
			},
		}
	}
</script>
