<template>
	<el-form :model="config.dataSource"
	         :rules="rules"
	         size="mini"
	         label-width="80px"
	         ref="dbSourceForm">
		<el-form-item label="域名/IP" prop="host">
			<el-input v-model="config.dataSource.host"></el-input>
		</el-form-item>
		<el-form-item label="端口" prop="port">
			<el-input v-model="config.dataSource.port"></el-input>
		</el-form-item>
		<el-form-item label="用户名" prop="userName">
			<el-input v-model="config.dataSource.userName"></el-input>
		</el-form-item>
		<el-form-item label="密码" prop="password">
			<el-input v-model="config.dataSource.password"></el-input>
		</el-form-item>
		<el-form-item label="数据库" prop="schema">
			<el-col :span="16">
				<el-select v-model="config.dataSource.schema"
				           placeholder="请选择数据库">
					<el-option v-for="(item, i) in schema"
					           :key="item.name"
					           :label="item.name"
					           :value="item.name">
					</el-option>
				</el-select>
			</el-col>
			<el-col :span="7" :offset="1">
				<el-button size="mini" type="primary" plain @click="initSchema">获取数据库列表</el-button>
			</el-col>
		</el-form-item>
	</el-form>
</template>

<script>
	import CONST from '../../../config/const'
	import API from '../../../config/api'

	export default {
		name: "DatabaseForm",
		props: ['config'],
		data() {
			return {
				rules: {
					host: [{required: true, message: '必须填写'}],
					port: [{required: true, message: '必须填写'}],
					userName: [{required: true, message: '必须填写'}],
					password: [{required: true, message: '必须填写'}],
					schema: [{required: true, message: '必须填写'}],
				},
				schema: []
			}
		},
		mounted() {
			if (this.config && this.config.dataSource.id && this.config.dataSource.id) {
				this.initSchema()
			}
		},
		methods: {
			/**
			 * 获得数据库列表
			 */
			initSchema() {
				this.schema = []
				let ds = this.config.dataSource
				this.ajax({
					url: API.DATA_SOURCE.LIST_SCHEMA,
					method: CONST.AJAX.POST,
					data: {
						host: ds.host,
						port: ds.port,
						userName: ds.userName,
						password: ds.password,
						type: ds.type
					},
				}).then(response => {
					this.schema = response.data
				})
			},
		}
	}
</script>

