<template>
	<div class="screen-design">
		<Menu :config="config" :state="state"></Menu>
		<Paint :config="config" :state="state"></Paint>
		<Layer :config="config" :state="state"></Layer>
		<Config :config="config" :state="state"></Config>
		<ContextMenu :config="config" :state="state"></ContextMenu>
		<Publisher :config="config" :state="publisher"></Publisher>
		<DataSourceEdit  v-if="state.addDataSourceModal.visible" :config="state.addDataSourceModal"></DataSourceEdit>
		<div class="action">
			<el-tooltip class="item" effect="dark" content="发布" placement="right">
				<el-button type="text" @click="publish">
					<i class="icon iconfont icon-plane"></i>
				</el-button>
			</el-tooltip>
			<el-tooltip class="item" effect="dark" content="保存" placement="right">
				<el-button type="text" @click="save">
					<i class="icon iconfont icon-save"></i>
				</el-button>
			</el-tooltip>
			<el-tooltip class="item" effect="dark" content="预览" placement="right">
				<el-button type="text" @click="preview">
					<i class="icon iconfont icon-view"></i>
				</el-button>
			</el-tooltip>
			<el-tooltip class="item" effect="dark" content="导出" placement="right">
				<el-button type="text" @click="exportConfig">
					<i class="icon iconfont icon-download"></i>
				</el-button>
			</el-tooltip>
			<el-tooltip class="item" effect="dark" content="退出" placement="right">
				<el-button type="text" @click="exit">
					<i class="icon iconfont icon-exit"></i>
				</el-button>
			</el-tooltip>
		</div>
	</div>
</template>

<script>
	import '../assets/styles/screen/design/main.scss'
	import Menu from '@/components/screen/Menu'
	import Layer from '@/components/screen/Layer'
	import Config from '@/components/screen/Config'
	import Paint from '@/components/screen/Paint'
	import ContextMenu from '@/components/screen/ContextMenu'
	import Publisher from '@/components/screen/Publisher'
	import DataSourceEdit from '@/components/datasource/DataSourceEdit'
	import API from '../config/api'
	import CONST from '../config/const'
	import _ from 'lodash'

	export default {
		name: 'Design',
		data() {
			return {
				// 储存配置页的状态
				state: null,
				// 页面配置文件
				config: null,
				// 发布弹窗
				publisher: {visible: false}
			}
		},
		created() {
			this.initScreen()
			// 点击任意位置取消选中组件
			window.addEventListener('click', this.cancelSelectComponent)
		},
		methods: {
			/**
			 * 根据url参数加载配置
			 */
			initScreen() {
				this.state = {
					// 当前选中的组件索引
					index: null,
					// 显示新增数据源窗口
					addDataSourceModal: {
						dataSource: null,
						visible: false
					},
					// 右键点击选中的组件索引
					contextMenu: {
						index: null,
						style: {top: 0, left: 0}
					},
				}
				this.config = {
					// 页面id
					id: null,
					// 页面名称
					name: '未命名',
					// 封面
					cover: null,
					// 状态 0-未发布 / 1-已发布
					state: 0,
					// 大屏配置
					base: {
						size: {width: 1800, height: 1200},
						backgroundColor: 'rgba(0,0,0,0)',
						backgroundImage: null,
						scaleMode: '0',
					},
					// 组件顺序
					sequence: [],
					// 组件
					components: {}
				}
				let id = this.$route.query.id
				if (!id) {
					return
				}
				this.ajax({
					url: API.SCREEN.GET_BY_ID,
					params: {id: id},
				}).then(response => {
					let config = response.data
					if (!config) {
						this.$message.error('错误：未找到可视化。')
					}
					config.base = JSON.parse(config.base)
					config.components = JSON.parse(config.components)
					config.sequence = JSON.parse(config.sequence)
					this.config = config
				})
			},
			/**
			 * 取消选中组件
			 * @param e
			 */
			cancelSelectComponent(e) {
				this.state.index = null
			},
			/**
			 * 保存
			 */
			save() {
				let config = this.formatConfigParam(this.config)
				this.ajax({
					url: API.SCREEN.SAVE,
					method: CONST.AJAX.POST,
					data: config,
				}).then(response => {
					if (!this.config.id) {
						this.config.id = response.data.id
					}
					this.$message(CONST.MESSAGE.SAVE_SUCCESS)
				})
			},
			/**
			 * 发布
			 */
			publish() {
				this.publisher.visible = true
			},
			/**
			 * 将可视化配置转为参数
			 */
			formatConfigParam(config) {
				let param = _.cloneDeep(config)
				for (let key in param) {
					let value = param[key]
					if (_.isObject(value)) {
						param[key] = JSON.stringify(value)
					}
				}
				return param
			},
			/**
			 * 预览
			 */
			preview() {
				let config = this.formatConfigParam(this.config)
				this.ajax({
					url: API.SCREEN.PREVIEW,
					method: CONST.AJAX.POST,
					data: config,
				}).then(response => {
					let link = this.$router.resolve({
						path: './presentation',
						query: {id: response.data.id}
					})
					window.open(link.href, '_blank')
				})
			},
			/**
			 * 导出
			 */
			exportConfig() {
				let config = _.cloneDeep(this.config)
				config.id = null
				let aTag = document.createElement('a')
				let blob = new Blob([JSON.stringify(config, null, 4)])
				aTag.download = "config-" + new Date().getTime()
				aTag.href = URL.createObjectURL(blob)
				aTag.click()
				URL.revokeObjectURL(blob)
			},
			/**
			 * 退出编辑页面
			 */
			exit() {
				this.$confirm('退出编辑界面会丢失所有未保存的操作, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$router.push('./overview')
				}).catch(() => {

				})
			}
		},
		components: {
			'Menu': Menu,
			'Layer': Layer,
			'Config': Config,
			'Paint': Paint,
			'Publisher': Publisher,
			'ContextMenu': ContextMenu,
			'DataSourceEdit': DataSourceEdit,
		}
	}
</script>

