					<el-input-number v-model="component.config.style.opacity"
					                 size="mini"
					                 controls-position="right" :min="0" :max="1" :step="0.05">
					</el-input-number>
					<div class="description">透明度</div>
				</el-col>
				<el-col :span="8">
				</el-col>
			</el-row>
		</el-collapse-item>
		<!-- 样式 -->
		<el-collapse-item title="样式" name="1">
			<el-collapse value="0">
				<el-collapse-item title="尺寸" name="0">
					<el-row>
						<el-col :span="8" class="tag">半径</el-col>
						<el-col :span="8">
							<el-input-number v-model="radius[0]"
							                 size="mini"
							                 controls-position="right"
							                 :min="0"
							                 :step="0.05"
							                 :max="1"
							                 @change="setRadius">
							</el-input-number>
							<div class="description">内圆</div>
						</el-col>
						<el-col :span="8">
							<el-input-number v-model="radius[1]"
							                 size="mini"
							                 controls-position="right"
							                 :min="0"
							                 :step="0.05"
							                 :max="1"
							                 @change="setRadius">
							</el-input-number>
							<div class="description">外圆</div>
						</el-col>
					</el-row>
				</el-collapse-item>
				<el-collapse-item title="引导线" name="1">
					<el-row>
						<el-col :span="8" class="tag">启用</el-col>
						<el-col :span="8">
							<el-checkbox v-model="component.config.option.series[0].label.normal.show"></el-checkbox>
						</el-col>
					</el-row>
					<el-row v-if="component.config.option.series[0].label.normal.show">
						<el-col :span="8" class="tag">线条</el-col>
						<el-col :span="8">
							<el-input-number v-model="component.config.option.series[0].labelLine.normal.lineStyle.width"
							                 size="mini"
							                 controls-position="right"
							                 :min="0"
							                 @change="changeLabelLineWidth">
							</el-input-number>
							<div class="description">高度</div>
						</el-col>
						<el-col :span="8">
							<el-color-picker v-model="component.config.option.series[0].labelLine.normal.lineStyle.color"
							                 size="mini"
							                 show-alpha
							                 @change="changeLabelLineColor"></el-color-picker>
							<div class="description">颜色</div>
						</el-col>
					</el-row>
					<el-row v-if="component.config.option.series[0].label.normal.show">
						<el-col :span="8" class="tag">文本</el-col>
						<el-col :span="8">
							<el-input-number v-model="component.config.option.series[0].label.normal.rich.b.fontSize"
							                 size="mini"
							                 controls-position="right"
							                 :min="0"
							                 @change="changeLabelFontSize">
							</el-input-number>
							<div class="description">大小</div>
						</el-col>
