					<el-row>
						<el-col :span="8" class="tag">垂直对齐</el-col>
						<el-col :span="8">
							<el-select v-model="component.config.option.legend.top"
							           placeholder="请选择"
							           size="mini">
								<el-option v-for="item in legend.alignVertical"
								           :key="item.value"
								           :label="item.label"
								           :value="item.value">
								</el-option>
							</el-select>
						</el-col>
					</el-row>
					<el-row>
						<el-col :span="8" class="tag">文本颜色</el-col>
						<el-col :span="8">
							<el-color-picker v-model="component.config.option.legend.textStyle.color"
							                 size="mini"
							                 show-alpha></el-color-picker>
						</el-col>
					</el-row>
				</el-collapse-item>
			</el-collapse>
		</el-collapse-item>
		<!-- 系列 -->
		<el-collapse-item title="数据系列" name="4">
			<el-collapse value="0">
				<el-collapse-item title="配色" name="0">
					<el-row>
						<el-col :span="4">
							<el-button type="text"
							           size="mini"
							           @click="addThemeColor">增加
							</el-button>
						</el-col>
						<el-col :span="4">
							<el-button type="text"
							           size="mini"
							           @click="minusThemeColor">减少
							</el-button>
						</el-col>
					</el-row>
					<el-row>
						<template v-for="(color, i) in component.config.option.series[0].color">
							<el-col :span="3">
								<el-color-picker v-model="component.config.option.series[0].color[i]"
								                 size="mini"
								                 show-alpha></el-color-picker>
							</el-col>
						</template>
					</el-row>
				</el-collapse-item>
			</el-collapse>
		</el-collapse-item>
	</el-collapse>
</template>

<script>
	import ColorUtils from '../../../../utils/ColorUtils'

	export default {
		name: 'BaseBar',
		props: ['component'],
		data() {
			return {
				radius: [],
				// 标签位置选项
				legend: {
					orient: [
						{label: '水平', value: 'horizontal'},
						{label: '垂直', value: 'vertical'},
					],
					alignHorizontal: [
						{label: '左侧', value: 'left'},
						{label: '居中', value: 'center'},
						{label: '右侧', value: 'right'},
					],
					alignVertical: [
						{label: '顶部', value: 'top'},
						{label: '居中', value: 'middle'},
						{label: '底部', value: 'bottom'},
					]
				}
			}
		},
		created() {
			let radius = this.component.config.option.series[0].radius
			this.radius = [this.percentToNumber(radius[0]), this.percentToNumber(radius[1])]
		},
		computed: {},
		methods: {
			/**
			 * 设置柱子宽度
			 */
			setRadius() {
				this.component.config.option.series[0].radius =
						[this.numberToPercent(this.radius[0]), this.numberToPercent(this.radius[1])]
			},
			/**
			 * 百分比转数字
			 * @param pct
			 * @returns {number}
			 */
			percentToNumber(pct) {
				if (!pct) {
					return 0
				}
				return parseInt(pct.substring(0, pct.length - 1)) / 100
			},
			/**
			 * 数字转百分比
			 * @param val
			 * @returns {string}
			 */
			numberToPercent(val) {
				if (!val) {
					val = 0
				}
				return val * 100 + '%'
			},
			/**
			 * 修改引导线颜色，同时修改label.normal.rich
			 */
			changeLabelLineWidth(value) {
				this.component.config.option.series[0].label.normal.rich.hr.borderWidth = value
			},
			/**
			 * 修改引导线粗细，同时修改label.normal.rich
			 */
			changeLabelLineColor(value) {
				this.component.config.option.series[0].label.normal.rich.hr.borderColor = value
			},
			/**
			 * 修改引导文本字体大小
			 */
			changeLabelFontSize(value) {
				this.component.config.option.series[0].label.normal.rich.d.fontSize = value
				this.component.config.option.series[0].label.normal.rich.c.fontSize = value
			},
			/**
			 * 修改引导文本字体颜色
			 */
			changeLabelFontColor(value) {
				this.component.config.option.series[0].label.normal.rich.d.color = value
				this.component.config.option.series[0].label.normal.rich.c.color = value
			},
			/**
			 * 增加主题颜色
			 */
			addThemeColor() {
				this.component.config.option.series[0].color.push(ColorUtils.random())
			},
			/**
			 * 减少主题颜色
			 */
			minusThemeColor() {
				this.component.config.option.series[0].color.pop()
			},
		},
		components: {},
	}
</script>
