<template>
	<Chart :config="config"></Chart>
</template>

<script>
	import Chart from '@/components/screen/layer/base/Chart'
	import _ from 'lodash'

	export default {
		name: 'BaseLine',
		props: ['config'],
		data() {
			return {}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					dataset: {
						dimensions: ['name', 'value'],
						source: [
							{name: '一月', value: 80},
							{name: '二月', value: 87},
							{name: '三月', value: 51},
							{name: '四月', value: 81},
							{name: '五月', value: 23},
							{name: '六月', value: 45},
						]
					},
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							lineStyle: {
								color: 'rgba(62, 158, 255, 0.8)',
								width: 1,
							}
						},
						backgroundColor: '#2F3943',
						borderColor: 'transparent',
						borderWidth: 1,
						padding: [5, 10, 5, 10],
						textStyle: {
							color: '#ffffff',
						},
						extraCssText: 'box-shadow: 0 0 5px rgba(0,0,0,0.3)'
					},
					legend: {
						orient: 'horizontal',
						top: 'top',
						left: 'center',
						textStyle: {
							fontSize: 12,
							color: ['#D4DFF5']
						},
						data: ['默认']
					},
					xAxis: {
						type: 'category',
						boundaryGap: false,
						splitLine: {
							show: true,
							interval: 'auto',
							lineStyle: {
								color: ['#D4DFF5']
							}
						},
						axisTick: {
							show: false
						},
						axisLine: {
							lineStyle: {
								color: '#609ee9'
							}
						},
						axisLabel: {
							show: true,
							margin: 10,
							textStyle: {
								fontSize: 14,
								color: '#dddddd'
							}
						}
					},
					yAxis: [{
						type: 'value',
						splitLine: {
							show: true,
							lineStyle: {
								color: ['#D4DFF5']
							}
						},
						axisTick: {
							show: false
						},
						axisLine: {
							lineStyle: {
								color: '#609ee9'
							}
						},
						axisLabel: {
							show: true,
							margin: 10,
							textStyle: {
								fontSize: 14,
								color: '#dddddd'
							}
						}
					}],
					series: [{
						name: '默认',
						type: 'line',
						smooth: true,
						showSymbol: false,
						symbol: 'circle',
						symbolSize: 6,
						areaStyle: {
							normal: {
								color: {
									x: 0,
									y: 0,
									x2: 0,
									y2: 1,
									type: 'linear',
									global: false,
									colorStops: [
										{offset: 0, color: 'rgba(62, 158, 255, 0.7)'},
										{offset: 0.5, color: 'rgba(62, 158, 255, 0.4)'},
										{offset: 1, color: 'rgba(0,0,0,0)'}
									]
								}
							}
						},
						itemStyle: {
							normal: {
								color: 'rgba(62, 158, 255, 1)'
							}
						},
						lineStyle: {
							normal: {
								width: 3
							}
						}
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: '一月', value: 80},
					{name: '二月', value: 87},
					{name: '三月', value: 51},
					{name: '四月', value: 81},
					{name: '五月', value: 23},
					{name: '六月', value: 45},
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [
					{
						name: 'click',
						comment: '点击事件',
						binders: [
							{name: 'name', comment: '数据名', prop: 'name', state: null},
							{name: 'value', comment: '数据值', prop: 'value', state: null},
						]
					}
				]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						name: null,
						value: null,
					}
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let dimensions = []
				for (let key in this.config.transform.mapper) {
					let dim = this.config.transform.mapper[key] || key
					if (!this.config.option.dataset.source[0].hasOwnProperty(dim)) {
						dim = null
					}
					dimensions.push(dim)
				}
				this.config.option.dataset = {
					dimensions: dimensions,
					source: this.config.data
				}
			}
		},
		components: {
			'Chart': Chart
		},
	}
</script>
