<template>
	<Plugin :config="config">
		<div class="timeline-ins" :class="config.option.vertical ? 'is-vertical' : ''">
			<div class="play-section" v-if="config.option.playable">
				<el-button size="mini"
				           type="text"
				           v-show="!state.playing"
				           @click="play">
					<i class="icon iconfont icon-play"></i>
				</el-button>
				<el-button size="mini"
				           type="text"
				           v-show="state.playing"
				           @click="stop">
					<i class="icon iconfont icon-pause"></i>
				</el-button>
			</div>
			<div class="time-section"
			     :style="calcProgressStyle"
			     v-if="config.option.data && config.option.data.length">
				<el-slider v-model="config.option.value"
				           :min="0"
				           :max="config.option.data.length - 1"
				           :step="1"
				           :vertical="config.option.vertical"
				           :height="config.style.progressHeight + 'px'"
				           :show-stops="true"
				           :show-tooltip="true"
				           :format-tooltip="formatTooltip"
				           @change="onEvent">
				</el-slider>
			</div>
		</div>
	</Plugin>
</template>

<script>
	import Plugin from '@/components/screen/layer/base/Plugin'
	import _ from 'lodash'

	export default {
		name: 'Timeline',
		props: ['config'],
		data() {
			return {
				state: {
					// 是否正在播放
					playing: false,
					// 播放进程
					thread: null,
				},
			}
		},
		created() {
			this.initStyle()
			this.initData()
			this.initTransform()
			this.initOption()
			this.initEvent()
			this.initProxy()
		},
		mounted() {
			this.onEvent(this.config.option.value)
		},
		computed: {
			/**
			 * 计算容器样式
			 */
			calcProgressStyle() {
				if (this.config.option.vertical) {
					return {
						height: this.config.style.progressHeight + 'px'
					}
				} else {
					return {
						width: this.config.style.progressWidth + 'px'
					}
				}
			},
		},
		methods: {
			/**
			 * 初始化样式
			 */
			initStyle() {
				this.config.style = _.assignIn({}, {
					progressWidth: 200,
					progressHeight: 200,
				}, this.config.style)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: '2011', value: '2011'},
					{name: '2012', value: '2012'},
					{name: '2013', value: '2013'},
					{name: '2014', value: '2014'},
					{name: '2015', value: '2015'},
					{name: '2016', value: '2016'},
					{name: '2017', value: '2017'},
					{name: '2018', value: '2018'},
				]
			},
			/**
			 * 声明默认option
			 */
			initOption() {
				this.config.option = _.assignIn({}, {
					// 初始位置
					start: 0,
					// 纵向轴
					vertical: false,
					// 可播放
					playable: true,
					// 播放间隔
					interval: 1,
					// 当前节点位置
					value: 0,
					// 循环播放
					loop: true,
					// 节点数据
					data: []
				}, this.config.option)
				this.config.option.value = this.config.option.start || 0
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [{
					name: 'node',
					comment: '节点事件',
					binders: [
						{name: 'name', comment: '文本', prop: 'name', state: null},
						{name: 'value', comment: '值', prop: 'value', state: null},
					]
				}]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = _.assignIn({}, {
					type: 1,
				}, this.config.proxy)
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = _.assignIn({}, {
					mapper: {
						name: null,
						value: null,
					}
				}, this.config.transform)
			},
			/**
			 * tooltip文本
			 */
			formatTooltip(index) {
				if (index === undefined || index === null) {
					return
				}
				return this.config.option.data[index].name
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let data = this.config.data
				let mapper = this.config.transform.mapper
				let propName = mapper.name || 'name'
				let optionData = []
				for (let i = 0; i < data.length; i++) {
					optionData.push({
						name: data[i][propName],
						value: i,
					})
				}
				this.config.option.data = optionData
			},
			/**
			 * 触发事件代理
			 */
			onEvent(index) {
				this.$children[0].onEvent('node', this.config.option.data[index], index)
			},
			/**
			 * 播放
			 */
			play() {
				if (this.config.option.value == this.config.option.data.length - 1) {
					this.config.option.value = 0
				}
				let me = this
				this.state.playing = true
				this.state.thread = setInterval(() => {
					me.config.option.value++
					if (me.config.option.value == me.config.option.data.length) {
						if (me.config.option.loop) {
							clearInterval(this.state.thread)
							me.config.option.value--
							me.play()
						} else {
							me.stop()
						}
					}
					me.onEvent(me.config.option.value)
				}, this.config.option.interval * 1000)
			},
			/**
			 * 暂停
			 */
			stop() {
				this.state.playing = false
				clearInterval(this.state.thread)
			},
		},
		watch: {
			'config.data': {
				deep: true,
				handler: function () {
					this.handler()
				}
			}
		},
		components: {
			'Plugin': Plugin
		},
	}
</script>
