<template>
	<div class="config-screen">
		<div class="title">页面设置</div>
		<el-row>
			<el-col :span="8" class="tag">名称</el-col>
			<el-col :span="16">
				<el-input v-model="config.name" size="mini"></el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8" class="tag">封面图</el-col>
			<el-col :span="16">
				<el-input v-model="config.cover"
				          placeholder="请输入图片网址"
				          size="mini" clearable></el-input>
				<el-upload drag
				           class="img-upload"
				           :headers="getToken()"
				           :action="getUploadUrl()"
				           :before-upload="beforeUpload"
				           :on-success="onSuccessCover"
				           :on-Error="onError"
				           :show-file-list="false">
					<img v-if="config.cover"
					     :src="config.cover"
					     class="img-preview">
					<i class="el-icon-upload"></i>
					<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
				</el-upload>
				<div v-if="config.cover"
				     class="img-cover">
					<i class="icon iconfont icon-delete" @click="removeCoverImage()"></i>
				</div>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8" class="tag">屏幕大小</el-col>
			<el-col :span="8">
				<el-input-number v-model="config.base.size.width"
				                 size="mini"
				                 controls-position="right"
				                 :min="0">
				</el-input-number>
				<div class="description">宽度</div>
			</el-col>
			<el-col :span="8">
				<el-input-number v-model="config.base.size.height"
				                 size="mini"
				                 controls-position="right"
				                 :min="0">
				</el-input-number>
				<div class="description">高度</div>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8" class="tag">背景色</el-col>
			<el-col :span="4">
				<el-color-picker v-model="config.base.backgroundColor"
				                 size="mini" show-alpha></el-color-picker>
			</el-col>
			<el-col :span="4">
				<el-button type="text" size="mini" @click="resetBgColor">重置</el-button>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8" class="tag">背景图</el-col>
			<el-col :span="16">
				<el-input v-model="config.base.backgroundImage"
				          placeholder="请输入图片网址"
				          size="mini" clearable></el-input>
				<el-upload drag
				           class="img-upload"
				           :headers="getToken()"
				           :action="getUploadUrl()"
				           :before-upload="beforeUpload"
				           :on-success="onSuccess"
				           :on-Error="onError"
				           :show-file-list="false">
					<img v-if="config.base.backgroundImage"
					     :src="config.base.backgroundImage"
					     class="img-preview">
					<i class="el-icon-upload"></i>
					<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
				</el-upload>
				<div v-if="config.base.backgroundImage"
				     class="img-cover">
					<i class="icon iconfont icon-delete" @click="removeBgImage()"></i>
				</div>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="8" class="tag">页面缩放方式</el-col>
			<el-col :span="16">
				<el-row>
					<el-radio v-model="config.base.scaleMode" label="0">等比缩放宽度铺满</el-radio>
				</el-row>
				<el-row>
					<el-radio v-model="config.base.scaleMode" label="1">等比缩放高度铺满</el-radio>
				</el-row>
				<el-row>
					<el-radio v-model="config.base.scaleMode" label="2">全屏铺满</el-radio>
				</el-row>
			</el-col>
		</el-row>
	</div>
</template>

<script>
	import API from '../../../config/api'
	import CONST from '../../../config/const'

	export default {
		name: "Screen",
		props: ["config", "state"],
		data() {
			return {
				default: {
					// 屏幕背景色
					bgColor: null
				}
			}
		},
		created() {
			this.default = {
				bgColor: this.config.base.backgroundColor
			}
		},
		methods: {
			resetBgColor() {
				this.config.base.backgroundColor = this.default.bgColor
			},
			/**
			 * 上传之前
			 */
			beforeUpload(file) {
				let name = file.name
				let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
				if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg') {
					this.$message({
						message: "只支持jpg、png、bmp文件",
						type: "error"
					})
					return false
				}
			},
			/**
			 * 获取上传地址
			 */
			getUploadUrl() {
				return API.ATTACHMENT.UPLOAD
			},
			/**
			 * 获取token作文参数上传
			 */
			getToken() {
				return {token: this.$store.state.token}
			},
			/**
			 * 上传成功
			 */
			onSuccess(res) {
				this.config.base.backgroundImage = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
			},
			/**
			 * 上传封面成功
			 */
			onSuccessCover(res) {
				this.config.cover = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
			},
			/**
			 * 上传失败
			 */
			onError(err) {
				this.$message({
					message: err.data.message,
					type: "error"
				})
			},
			/**
			 * 移除背景图
			 */
			removeBgImage() {
				this.config.base.backgroundImage = null
			},
			/**
			 * 移除封面图
			 */
			removeCoverImage() {
				this.config.cover = null
			}
		}
	}
</script>

