<template>
	<el-form :model="config.dataSource"
	         :rules="rules"
	         size="mini"
	         label-width="80px">
		<el-form-item label="" prop="attachment">
			<el-upload drag
			           :limit="1"
			           :headers="getToken()"
			           :action="getUploadUrl()"
			           :before-upload="beforeUpload"
			           :on-remove="remove"
			           :on-success="onSuccess"
			           :on-exceed="onExceed"
			           :on-Error="onError">
				<i class="el-icon-upload"></i>
				<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
				<div class="el-upload__tip" slot="tip">只能上传xls、xlsx、csv文件</div>
			</el-upload>
		</el-form-item>
	</el-form>
</template>

<script>
	import API from '../../../config/api'
	import CONST from '../../../config/const'

	export default {
		name: "FileForm",
		props: ['config'],
		data() {
			let validateAttachment = (rule, value, callback) => {
				if (!this.config.dataSource.attachmentId) {
					callback(new Error('请上传文件。'))
				} else {
					callback()
				}
			}
			return {
				rules: {
					attachment: [
						{ validator: validateAttachment, trigger: 'change' }
					]
				},
			}
		},
		methods: {
			/**
			 * 上传之前
			 */
			beforeUpload(file) {
				let name = file.name
				let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
				if (ext !== "xls" && ext !== "xlsx" && ext !== 'csv') {
					this.$message({
						message: "只支持xls、xlsx、csv文件",
						type: "error"
					})
					return false
				}
			},
			/**
			 * 获取上传地址
			 */
			getUploadUrl() {
				return API.ATTACHMENT.UPLOAD
			},
			/**
			 * 获取token作文参数上传
			 */
			getToken() {
				return {token: this.$store.state.token}
			},
			/**
			 * 删除文件
			 */
			remove(file, fileList) {
				if (!this.config.dataSource.id) {
					return
				}
				this.ajax({
					url: API.ATTACHMENT.DELETE,
					params: {id: this.config.dataSource.id}
				}).then(response => {
					if (response.data.success) {
						this.$message(CONST.MESSAGE.DELETE_SUCCESS)
					}
				})
				this.config.dataSource.id = null
			},
			/**
			 * 上传成功
			 */
			onSuccess(res) {
				this.config.dataSource.attachmentId = res.data.id
			},
			/**
			 * 上传失败
			 */
			onError(err) {
				this.$message({
					message: err.data.message,
					type: "error"
				})
			},
			/**
			 * 文件超出限制
			 */
			onExceed(files, fileList) {
				this.$message({
					message: "最多只可上传1个文件，请删除当前文件后再上传",
					type: "error"
				})
			}
		}
	}
</script>

