class Base {

	/**
	 * 遍历所有参数，其中以冒号(:)开头的变量是引用全局状态，
	 * 将全局状态存储到config.store属性中，并当前组件注册到vuex store，
	 * 订阅该全局状态的变更消息，未来当全局状态变更时，分发消息到当前组件的config.store中
	 * 当前组件通过监听config.store属性的变化，来更新组件状态
	 */
	constructor(component) {
		this.component = component
	}

	do() {
		throw new Error("DataSource错误：未声明代理方法do")
	}
}

export default Base
